import { Injectable, Logger } from '@nestjs/common';
import { RedisService } from '../../common/services/redis.service';

export interface BankSessionConfig {
  bankCode: string;
  sessionTimeoutMs: number; // Session timeout in milliseconds
  refreshBeforeMs: number; // Refresh session before this time (e.g., 1 minute before)
}

export interface DeviceSession {
  deviceId: string;
  bankCode: string;
  loginTime: number; // Timestamp when logged in
  expiresAt: number; // Timestamp when session expires
  status: 'logged_in' | 'logging_in' | 'expired';
}

@Injectable()
export class SessionManagementService {
  private readonly logger = new Logger('SessionManagementService');
  private readonly REDIS_SESSION_PREFIX = 'bank:session:';
  
  // Session timeout configuration for each bank (in milliseconds)
  private readonly BANK_SESSION_CONFIGS: Map<string, BankSessionConfig> = new Map([
    [
      'ACB',
      {
        bankCode: 'ACB',
        sessionTimeoutMs: 15 * 60 * 1000, // 15 minutes
        refreshBeforeMs: 1 * 60 * 1000, // Refresh 1 minute before expiry
      },
    ],
    [
      'PGBANK',
      {
        bankCode: 'PGBANK',
        sessionTimeoutMs: 4.5 * 60 * 1000, // 4.5 minutes
        refreshBeforeMs: 30 * 1000, // Refresh 30 seconds before expiry
      },
    ],
    [
      'HDBANK',
      {
        bankCode: 'HDBANK',
        sessionTimeoutMs: 7.5 * 60 * 1000, // 4.5 minutes
        refreshBeforeMs: 30 * 1000, // Refresh 30 seconds before expiry
      },
    ],
  ]);

  constructor(private readonly redisService: RedisService) {}

  /**
   * Get session timeout for a bank
   */
  getSessionTimeout(bankCode: string): number {
    const config = this.BANK_SESSION_CONFIGS.get(bankCode.toUpperCase());
    return config?.sessionTimeoutMs || 4.5 * 60 * 1000; // Default 4.5 minutes
  }

  /**
   * Get refresh before time for a bank
   */
  getRefreshBeforeTime(bankCode: string): number {
    const config = this.BANK_SESSION_CONFIGS.get(bankCode.toUpperCase());
    return config?.refreshBeforeMs || 30 * 1000; // Default 30 seconds
  }

  /**
   * Record login session for a device
   */
  async recordLogin(deviceId: string, bankCode: string): Promise<void> {
    const timeout = this.getSessionTimeout(bankCode);
    const expiresAt = Date.now() + timeout;
    
    const session: DeviceSession = {
      deviceId,
      bankCode: bankCode.toUpperCase(),
      loginTime: Date.now(),
      expiresAt,
      status: 'logged_in',
    };

    const key = `${this.REDIS_SESSION_PREFIX}${deviceId}:${bankCode.toUpperCase()}`;
    await this.redisService.set(key, JSON.stringify(session), timeout);
    
    this.logger.log(`[${deviceId}] Recorded login session (bank: ${bankCode}, expires at: ${new Date(expiresAt).toISOString()})`);
  }

  /**
   * Mark device as logging in
   */
  async markLoggingIn(deviceId: string, bankCode: string): Promise<void> {
    const key = `${this.REDIS_SESSION_PREFIX}${deviceId}:${bankCode.toUpperCase()}`;
    const existing = await this.getSession(deviceId, bankCode);
    
    const session: DeviceSession = {
      deviceId,
      bankCode: bankCode.toUpperCase(),
      loginTime: existing?.loginTime || Date.now(),
      expiresAt: existing?.expiresAt || Date.now() + this.getSessionTimeout(bankCode),
      status: 'logging_in',
    };

    await this.redisService.set(key, JSON.stringify(session), 5 * 60 * 1000); // 5 min TTL for login process
    this.logger.log(`Marked device ${deviceId}, bank ${bankCode} as logging in`);
  }

  /**
   * Get current session for a device
   */
  async getSession(deviceId: string, bankCode: string): Promise<DeviceSession | null> {
    try {
      const key = `${this.REDIS_SESSION_PREFIX}${deviceId}:${bankCode.toUpperCase()}`;
      const data = await this.redisService.get(key);
      
      if (!data) {
        return null;
      }

      return JSON.parse(data) as DeviceSession;
    } catch (error) {
      this.logger.error(`Error getting session for device ${deviceId}, bank ${bankCode}: ${error.message}`);
      return null;
    }
  }

  /**
   * Check if session is valid (not expired and not logging in)
   */
  async isSessionValid(deviceId: string, bankCode: string): Promise<boolean> {
    const session = await this.getSession(deviceId, bankCode);
    
    if (!session) {
      return false;
    }

    // Check if session is expired
    if (session.expiresAt < Date.now()) {
      return false;
    }

    // Check if device is currently logging in
    if (session.status === 'logging_in') {
      return false;
    }

    return session.status === 'logged_in';
  }

  /**
   * Check if session needs refresh (within refresh window)
   */
  async needsRefresh(deviceId: string, bankCode: string): Promise<boolean> {
    const session = await this.getSession(deviceId, bankCode);
    
    if (!session || session.status !== 'logged_in') {
      return false;
    }

    const refreshBeforeTime = this.getRefreshBeforeTime(bankCode);
    const timeUntilExpiry = session.expiresAt - Date.now();
    
    return timeUntilExpiry <= refreshBeforeTime;
  }

  /**
   * Clear session
   */
  async clearSession(deviceId: string, bankCode: string): Promise<void> {
    const key = `${this.REDIS_SESSION_PREFIX}${deviceId}:${bankCode.toUpperCase()}`;
    await this.redisService.del(key);
    this.logger.log(`Cleared session for device ${deviceId}, bank ${bankCode}`);
  }
}

