import { Module } from '@nestjs/common';
import { BullModule } from '@nestjs/bull';
import { ScheduleModule } from '@nestjs/schedule';
import { HttpModule } from '@nestjs/axios';

import { WithdrawalModule } from './modules/withdrawal/withdrawal.module';
import { HcBoxModule } from './modules/hcbox/hcbox.module';
import { DeviceModule } from './modules/device/device.module';
import { RedisService } from './common/services/redis.service';
import { WebSocketModule } from './common/modules/websocket/websocket.module';
import { config } from './common/constants/config';
import { BankModule } from './modules/bank/bank.module';
import { OCRModule } from './modules/ocr/ocr.module';
import { NotificationModule } from './modules/notifiction/notification.module';
import { WindowAppModule } from './modules/window-app/window-app.module';
import { RedisLockModule } from './modules/redis-lock/redis-lock.module';

@Module({
  imports: [
    BullModule.forRoot({
      redis: {
        host: config.redis.host,
        port: config.redis.port,
        db: config.redis.db,
        password: config.redis.password,
      },
      defaultJobOptions: {
        removeOnComplete: 10, // Keep last 10 completed jobs
        removeOnFail: 50, // Keep last 50 failed jobs
        attempts: 3, // Retry failed jobs 3 times
        backoff: {
          type: 'exponential',
          delay: 2000, // 2 seconds initial delay
        },
      },
    }),

    // Scheduling
    ScheduleModule.forRoot(),

    // HTTP client
    HttpModule,

    // Feature modules
    WithdrawalModule,
    HcBoxModule,
    DeviceModule,
    WebSocketModule,
    BankModule,
    OCRModule,
    NotificationModule,
    RedisLockModule
  ],
  providers: [
    RedisService,
  ],
  exports: [RedisService],
})
export class AppModule {}

