import { Inject, Injectable, Logger } from '@nestjs/common';
import { Withdrawal } from '../../common/interfaces/withdrawal.interface';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';
import { ICaptureScreenOptions, IHCSwipeOptions, IPandaSwipeOptions, IWebSocketService } from '@/common/modules/websocket/interface/websocket.interface';
import { sleep } from '@/common/utils/time';

@Injectable()
export class HcBoxService {
  private readonly logger = new Logger('HcBoxService');

  constructor(
    @Inject(WEBSOCKET_SERVICE)
    private readonly wsService: IWebSocketService,
  ) {}

  async listDevices() {
    try {
      return await this.wsService.listDevices();
    } catch (error) {
      this.logger.error(`Error listing devices: ${error.message}`);
      throw error;
    }
  }

  async executeTransfer(withdrawal: Withdrawal, deviceId: string): Promise<boolean> {
    try {
      this.logger.log(`Executing transfer for withdrawal ${withdrawal.withdrawalId} on device ${deviceId}`);

      // Step 1: Launch bank app (example bundle ID for Vietcombank app)
      // Note: This is a placeholder - actual bundle ID should be configured per bank
      const bankBundleId = this.getBankBundleId(withdrawal.bankCode);
      if (bankBundleId) {
        await this.wsService.launchApp(deviceId, bankBundleId);
        await sleep(3000); // Wait for app to launch
      }

      // Step 2: Navigate to transfer screen
      // This is a simplified flow - actual implementation would need to know the app's UI structure
      // Click on transfer/money transfer button (example coordinates - should be configured)
      await this.wsService.click(deviceId, 0.5, 0.3, 0.2); // Example: center-top area
      await sleep(2000);

      // Step 3: Enter recipient account number
      // This would require OCR or predefined coordinates based on the app
      // For now, this is a placeholder that needs to be customized per bank app
      this.logger.warn('Transfer execution flow needs to be customized per bank app UI');

      // Step 4: Enter amount
      // Step 5: Enter description
      // Step 6: Confirm transfer

      // For now, return true as a placeholder
      // In production, this should verify the transfer was successful
      this.logger.log(`Transfer execution completed for withdrawal ${withdrawal.withdrawalId}`);
      return true;
    } catch (error) {
      this.logger.error(`Error executing transfer for withdrawal ${withdrawal.withdrawalId}: ${error.message}`);
      throw error;
    }
  }

  private getBankBundleId(bankCode: string): string | null {
    // Map bank codes to iOS app bundle IDs
    // These are examples and should be configured properly
    const bundleIds: Record<string, string> = {
      VIETCOMBANK: 'com.vietcombank.mobilebanking',
      TPBANK: 'com.tpbank.mobilebanking',
      TECHCOMBANK: 'com.techcombank.mobilebanking',
      // Add more banks as needed
    };

    return bundleIds[bankCode] || null;
  }

  // Expose WebSocket service methods for direct use if needed
  async click(deviceId: string, x: number, y: number, duration = 0.2): Promise<string> {
    return this.wsService.click(deviceId, x, y, duration);
  }

  async swipe(deviceId: string, options: IHCSwipeOptions | IPandaSwipeOptions): Promise<string> {
    return this.wsService.swipe(deviceId, options);
  }

  async screen(deviceId: string, options: ICaptureScreenOptions): Promise<string> {
    return this.wsService.captureScreen(deviceId, options);
  }

  async home(deviceId: string): Promise<string> {
    return this.wsService.home(deviceId);
  }

  async inputNumpadNumber(deviceId: string, value: string): Promise<void> {
    return this.wsService.inputNumpadNumber(deviceId, value);
  }

  inputText(deviceId: string, text: string): Promise<void> {
    return this.wsService.inputText(deviceId, text);
  }
}

