import { forwardRef, HttpException, HttpStatus, Inject, Injectable, Logger, OnModuleInit } from '@nestjs/common';
import { RedisService } from '../../common/services/redis.service';
import { DeviceBankCode, DeviceConfig, UpdateDeviceConfigDto } from './dto/device-config.dto';
import { IWebSocketService } from '@/common/modules/websocket/interface/websocket.interface';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';
import { DeviceLockService } from './device-lock.service'
import { BankService } from '../bank/bank.service';
import { isNil, omit } from 'lodash';

@Injectable()
export class DeviceService implements OnModuleInit {
  private readonly logger = new Logger('DeviceService');
  private readonly REDIS_DEVICE_CONFIG_PREFIX = 'device:';
  private readonly REDIS_DEVICE_CONFIG_SUFFIX = ':config';
  private readonly REDIS_SERVICE_STATUS_KEY = 'service:status';

  constructor(
    private readonly redisService: RedisService,
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
    private readonly deviceLockService: DeviceLockService,
    @Inject(forwardRef(() => BankService))
    private readonly bankService: BankService,
  ) {}

  async onModuleInit() {
    // Always set service status to 'inactive' on server startup
    try {
      await this.setServiceStatus('inactive');
      this.logger.log('Service status initialized to: inactive');
    } catch (error) {
      this.logger.error(`Failed to initialize service status: ${error.message}`);
    }
  }

  async listDeviceConfigs(): Promise<DeviceConfig[]> {
    try {
      // Get all keys matching pattern device:*:config
      const pattern = `${this.REDIS_DEVICE_CONFIG_PREFIX}*${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      const keys = await this.redisService.keys(pattern);
      
      if (keys.length === 0) {
        return [];
      }

      // Get all configs in parallel
      const configs = await Promise.all(
        keys.map(async (key) => {
          try {
            const data = await this.redisService.get(key);
            if (!data) {
              return null;
            }
            return JSON.parse(data) as DeviceConfig;
          } catch (error) {
            this.logger.error(`Error parsing config for key ${key}: ${error.message}`);
            return null;
          }
        }),
      );

      // Filter out null values and return
      return configs.filter((config) => !isNil(config));
    } catch (error) {
      this.logger.error(`Error listing device configs: ${error.message}`);

      return [];
    }
  }

  async listDevices() {
    try {
      const devices = await this.wsService.listDevices();
      
      // Map bankCode and config from Redis for each device
      // BankCode mapping is stored in Redis: device:{deviceId}:config
      const devicesWithConfig = await Promise.all(
        devices.map(async (device: any) => {
          // Get saved config from Redis (includes bankCode mapping)
          const config = await this.getDeviceConfig(device.udid);
          
            return {
            ...device,
            // Map bankCode from Redis config (persistent mapping)
            bankCode: config?.bankCode || '',
            availableAmount: config?.availableAmount || 0,
            status: config?.status || 'deactive',
            password: config?.password || '',
            smartOTP: config?.smartOTP || '',
            accountNo: config?.accountNo || '',
            accountName: config?.accountName || '',
            transferLimitPerDay: config?.transferLimitPerDay,
            doesHandleLargeAmount: config?.doesHandleLargeAmount,
            minTransferAmount: config?.minTransferAmount,
          };
        }),
      );

      return devicesWithConfig;
    } catch (error) {
      this.logger.error(`Error listing devices: ${error.message}`);
      
      return [];
    }
  }

  async getDeviceConfig(deviceId: string): Promise<DeviceConfig | null> {
    try {
      const key = `${this.REDIS_DEVICE_CONFIG_PREFIX}${deviceId}${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      const data = await this.redisService.get(key);
      
      if (!data) {
        return null;
      }

      return JSON.parse(data) as DeviceConfig;
    } catch (error) {
      this.logger.error(`Error getting device config for ${deviceId}: ${error.message}`);
      return null;
    }
  }

  async getDeviceConfigSafe(deviceId: string): Promise<DeviceConfig | null> {
    try {
      const config = await this.getDeviceConfig(deviceId);
      if (!config) {
        return null;
      }

      // Hide password and smartOTP from response
      return {
        ...config,
        password: config.password ? '' : undefined,
        smartOTP: config.smartOTP ? '' : undefined,
      };
    } catch (error) {
      this.logger.error(`Error getting safe device config for ${deviceId}: ${error.message}`);
      return null;
    }
  }

  async updateDeviceConfig(deviceId: string, dto: UpdateDeviceConfigDto): Promise<DeviceConfig> {
    try {
      const key = `${this.REDIS_DEVICE_CONFIG_PREFIX}${deviceId}${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      
      // Get existing config or create new
      const existing = await this.getDeviceConfig(deviceId);
      
      // If device already has config, password and smartOTP cannot be updated
      // Always keep existing password and smartOTP if config exists
      const hasExistingConfig = existing?.password || existing?.smartOTP;
      const password = hasExistingConfig 
        ? (existing?.password || '')
        : (dto.password !== undefined ? dto.password : '');
      const smartOTP = hasExistingConfig 
        ? (existing?.smartOTP || '')
        : (dto.smartOTP !== undefined ? dto.smartOTP : '');
      
      const config: DeviceConfig = {
        deviceId,
        // BankCode mapping: save once, persist in Redis
        bankCode: dto.bankCode !== undefined ? dto.bankCode : existing?.bankCode || '' as DeviceBankCode,
        availableAmount: dto.availableAmount !== undefined ? dto.availableAmount : existing?.availableAmount || 0,
        status: dto.status !== undefined ? dto.status : existing?.status || 'deactive',
        password,
        smartOTP,
        accountNo: dto.accountNo !== undefined ? dto.accountNo : existing?.accountNo || '',
        accountName: dto.accountName !== undefined ? dto.accountName : existing?.accountName || '',
        transferLimitPerDay: dto.transferLimitPerDay !== undefined ? dto.transferLimitPerDay : existing?.transferLimitPerDay,
        doesHandleLargeAmount: dto.doesHandleLargeAmount !== undefined ? dto.doesHandleLargeAmount : existing?.doesHandleLargeAmount,
        minTransferAmount: dto.minTransferAmount !== undefined ? dto.minTransferAmount : existing?.minTransferAmount,
      };

      // Save to Redis (no expiration - persistent config including bankCode mapping)
      // Key format: device:{deviceId}:config
      await this.redisService.set(key, JSON.stringify(config));

      this.logger.log(`[${deviceId}] Updated device config (bankCode: ${config.bankCode || 'not set'}): ${JSON.stringify(omit(config, ['password', 'smartOTP']))}`);
      return config;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error updating device config: ${error.message}`);
      throw error;
    }
  }

  async deleteDeviceConfig(deviceId: string): Promise<void> {
    try {
      const key = `${this.REDIS_DEVICE_CONFIG_PREFIX}${deviceId}${this.REDIS_DEVICE_CONFIG_SUFFIX}`;
      await this.redisService.del(key);

      this.logger.log(`[${deviceId}] Deleted device config`);
    } catch (error) {
      this.logger.error(`[${deviceId}] Error deleting device config: ${error.message}`);
      throw error;
    }
  }

  async getServiceStatus(): Promise<'active' | 'inactive'> {
    try {
      const status = await this.redisService.get(this.REDIS_SERVICE_STATUS_KEY);
      return (status as 'active' | 'inactive') || 'inactive';
    } catch (error) {
      this.logger.error(`Error getting service status: ${error.message}`);
      return 'inactive';
    }
  }

  async setServiceStatus(status: 'active' | 'inactive'): Promise<void> {
    try {
      await this.redisService.set(this.REDIS_SERVICE_STATUS_KEY, status);
      this.logger.log(`Service status set to: ${status}`);
    } catch (error) {
      this.logger.error(`Error setting service status: ${error.message}`);
      throw error;
    }
  }

  async startService() {
    try {
      // Check if there is at least one device with status = 'active'
      const hasActiveDevice = await this.hasActiveDevice();

      if (!hasActiveDevice) {
        throw new HttpException(
          {
            success: false,
            error: 'Vui lng active bank trc khi start',
          },
          HttpStatus.BAD_REQUEST,
        );
      }

      // Login all active banks (Non-blocking: don't await completion)
      // This allows devices to start working individually as soon as they finish login
      this.bankService.loginAllActiveBanks().catch(err => {
        this.logger.error(`Error during background login process: ${err.message}`);
      });

      // Give a small buffer for logins to acquire locks before enabling service
      await new Promise(resolve => setTimeout(resolve, 2000));

      // Set service status to active
      await this.setServiceStatus('active');

      // Trigger job processing immediately
      // (Optional: fetch JobManager and trigger? But cron will pick it up in <10s)

      return {
        success: true,
        message: 'Service started successfully. Devices are logging in and will accept jobs when ready.',
      };
    } catch (error) {
      if (error instanceof HttpException) {
        throw error;
      }
      this.logger.error(`Error starting service: ${error.message}`, error.stack);
      throw new HttpException(
        {
          success: false,
          error: error.message || 'Failed to start service',
        },
        HttpStatus.INTERNAL_SERVER_ERROR,
      );
    }
  }

  async deductDeviceAmount(deviceId: string, amount: number): Promise<boolean> {
    try {
      const config = await this.getDeviceConfig(deviceId);
      if (!config) {
        return false;
      }

      const newAmount = config.availableAmount - amount;
      if (newAmount < 0) {
        this.logger.warn(`Insufficient amount for device ${deviceId}. Available: ${config.availableAmount}, Requested: ${amount}`);
        return false;
      }

      await this.updateDeviceConfig(deviceId, { availableAmount: newAmount });
      return true;
    } catch (error) {
      this.logger.error(`Error deducting amount for device ${deviceId}: ${error.message}`);
      return false;
    }
  }

  /**
   * Check if there is at least one device with status = 'active'
   */
  async hasActiveDevice(): Promise<boolean> {
    try {
      const devices = await this.listDevices();
      return devices.some(device => device.status === 'active');
    } catch (error) {
      this.logger.error(`Error checking active devices: ${error.message}`);
      return false;
    }
  }

  /**
   * Get list of available devices (Active AND Unlocked)
   */
  async getAvailableDevices() {
    try {
      const devices = await this.wsService.listDevices();
      const availableDevices: Array<DeviceConfig> = [];

      for (const device of devices) {
        // 1. Check Config Status
        const deviceConfig = await this.getDeviceConfig(device.udid);

        if (deviceConfig?.status !== 'active') {
          continue;
        }

        // 2. Check Lock Status (Must be free)
        const isLocked = await this.deviceLockService.isLocked(device.udid);
        if (isLocked) {
          // this.logger.debug(`Device ${device.udid} is active but LOCKED. Skipping.`);
          continue;
        }

        // 3. Check session of bank because cron for login run every 30 seconds but cron for pick job run every 5 seconds
        const hasValidSession = await this.bankService.isSessionValid(device.udid, deviceConfig.bankCode);
        if (!hasValidSession) {
          this.logger.error(`[${device.udid}] Device does not have valid session for bank ${deviceConfig.bankCode}`);

          continue;
        }

        availableDevices.push(deviceConfig);
      }

      return availableDevices;
    } catch (error) {
      this.logger.error(`Error getting available devices: ${error.message}`);
      return [];
    }
  }
}

