import { Injectable, Logger, Inject, forwardRef } from '@nestjs/common';
import { BankService } from '../bank/bank.service';
import { DeviceService } from '../device/device.service';
import { RedisService } from '../../common/services/redis.service';
import { Withdrawal } from '../../common/interfaces/withdrawal.interface';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';
import { IWebSocketService } from '@/common/modules/websocket/interface/websocket.interface';
import { WithdrawalProcessingError, ErrorType } from './withdrawal-errors';
import { NotificationService } from '../notifiction/notification.service';
import { firstValueFrom } from 'rxjs';
import { config } from '@/common/constants/config';
import { HttpService } from '@nestjs/axios';
import { WithdrawalStatus } from './constants/withdrawal-status';
import { DeviceConfig } from '../device/dto/device-config.dto';
import { formatCurrency } from '@/common/utils/currency';

@Injectable()
export class WithdrawalService {
  private readonly logger = new Logger('WithdrawalService');
  private readonly REDIS_PROCESSED_PREFIX = 'withdrawal:processed:';
  private readonly PROCESSING_TTL = 86400 * 1000 * 3; // 3 days in milliseconds
  private readonly COMPLETED_TTL = 86400 * 1000 * 3; // 3 days in milliseconds

  constructor(
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
    private readonly bankService: BankService,
    @Inject(forwardRef(() => DeviceService))
    private readonly deviceService: DeviceService,
    private readonly redisService: RedisService,
    private readonly notificationService: NotificationService,
    private readonly httpService: HttpService
  ) { }

  /**
   * Check if withdrawal was already processed
   */
  private async checkIfProcessed(withdrawalId: string): Promise<boolean> {
    try {
      const key = `${this.REDIS_PROCESSED_PREFIX}${withdrawalId}`;
      const status = await this.redisService.get(key);

      if (status === 'completed') {
        this.logger.warn(`Withdrawal ${withdrawalId} already completed`);
        return true;
      }

      if (status === 'processing') {
        this.logger.warn(`Withdrawal ${withdrawalId} is being processed by another instance`);
        return true;
      }

      return false;
    } catch (error) {
      this.logger.error(`Error checking processed status for ${withdrawalId}: ${error.message}`);
      // If Redis check fails, allow processing (fail open)
      return false;
    }
  }

  /**
   * Mark withdrawal as processing (atomic operation using SETNX)
   */
  private async markAsProcessing(withdrawalId: string): Promise<boolean> {
    try {
      const key = `${this.REDIS_PROCESSED_PREFIX}${withdrawalId}`;
      // Use SETNX (SET if Not eXists) - atomic operation
      // This ensures only one instance can mark as processing
      const result = await this.redisService.setnx(key, 'processing', this.PROCESSING_TTL);

      if (result === 'OK') {
        this.logger.debug(`Marked withdrawal ${withdrawalId} as processing`);
        return true;
      }

      // Key already exists - another instance is processing
      this.logger.warn(`Withdrawal ${withdrawalId} is already being processed by another instance`);

      return false;
    } catch (error) {
      this.logger.error(`Error marking withdrawal ${withdrawalId} as processing: ${error.message}`);
      // If Redis fails, allow processing (fail open)
      return true;
    }
  }

  /**
   * Mark withdrawal as completed
   */
  private async markAsCompleted(withdrawalId: string): Promise<void> {
    try {
      const key = `${this.REDIS_PROCESSED_PREFIX}${withdrawalId}`;
      await this.redisService.set(key, 'completed', this.COMPLETED_TTL);
      this.logger.log(`Marked withdrawal ${withdrawalId} as completed`);
    } catch (error) {
      this.logger.error(`Error marking withdrawal ${withdrawalId} as completed: ${error.message}`);
    }
  }

  /**
   * Mark withdrawal as failed (remove from Redis to allow manual retry)
   */
  private async markAsFailed(withdrawalId: string): Promise<void> {
    try {
      const key = `${this.REDIS_PROCESSED_PREFIX}${withdrawalId}`;
      await this.redisService.del(key);
      this.logger.log(`Marked withdrawal ${withdrawalId} as failed (removed from processed list)`);
    } catch (error) {
      this.logger.error(`Error marking withdrawal ${withdrawalId} as failed: ${error.message}`);
    }
  }

  async processWithdrawal(withdrawal: Withdrawal, deviceConfig: DeviceConfig) {
    const { withdrawalId, bankCode, amount } = withdrawal;

    // 1. IDEMPOTENCY CHECK - m bo withdrawal cha c x l
    const isProcessed = await this.checkIfProcessed(withdrawalId);
    if (isProcessed) {
      this.logger.warn(`Withdrawal ${withdrawalId} already processed or being processed, skipping`);

      return;
    }

    // 2. Mark as processing (atomic operation)
    const markAsProcessing = await this.markAsProcessing(withdrawalId);
    if (!markAsProcessing) {
      const reason = `Withdrawal ${withdrawalId} is being processed by another instance`;
      this.logger.warn(reason);
      // Throw error vi type TEMPORARY v c th retry sau khi instance khc xong
      throw new WithdrawalProcessingError(
        reason, 
        ErrorType.TEMPORARY,
        'Failed: Mark as processing'
      );
    }

    try {
      this.logger.log(`[${deviceConfig.accountName}] Processing withdrawal: ${withdrawalId} for bank: ${bankCode}, amount: ${amount}`);

      // TODO: 1 Auto find available balance for device
      // Verify available amount
      if (deviceConfig.availableAmount < amount) {
        const reason = `[${deviceConfig.accountName}] Assigned device has insufficient amount. Available: ${deviceConfig.availableAmount}, Required: ${amount}`;
        this.logger.error(reason);

        await this.markAsFailed(withdrawalId);

        const isAmountUnder5M = deviceConfig.availableAmount < 5_000_000;

        if (isAmountUnder5M) {
          await this.deviceService.updateDeviceConfig(deviceConfig.deviceId, { status: 'deactive' });
        }
        
        throw new WithdrawalProcessingError(
          reason, ErrorType.TEMPORARY,
          isAmountUnder5M ? `Failed: Khng  s d (Di 5M)` : `Failed: Khng  s d ${formatCurrency(deviceConfig.availableAmount)}`
        );
      }

      await this.bankService.executeTransferWithQRCode(withdrawal, deviceConfig, deviceConfig.bankCode);

      this.deviceService.deductDeviceAmount(deviceConfig.deviceId, amount).catch(() => {});

      const analysisResult = await this.bankService.analyzeTransferBill(deviceConfig.bankCode, withdrawal);

      this.notificationService.completeWithdrawal(withdrawal, analysisResult, deviceConfig).catch(() => {});

      if (analysisResult.analyzedStatus === 'unknown') {
        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);
      }

      await this.bankService.goToHomeScreenFromBill(deviceConfig);

      await this.markAsCompleted(withdrawalId);

      this.logger.log(`Successfully processed withdrawal: ${withdrawalId}`);
    } catch (error) {
      await this.markAsFailed(withdrawalId);
      
      throw error;
    }
  }

  async getWithdrawalDetails(id: string) {
    const url = `${config.ezpayBe.apiUrl}/api/internal/withdrawals/${id}`;

    const response = await firstValueFrom(
      this.httpService.get<{
        data: { 
          status: WithdrawalStatus; 
          withdrawalId: string; 
          withdrawalCode: string,
          cryptoExchangeId: string;
          bankCode: string;
          beneficiaryAccountNo: string;
          beneficiaryName: string;
          amount: string;
          /* ... add more field if needed, the api will return more */
        }
      }>(url, {
        headers: {
          'Content-Type': 'application/json',
          'X-API-Key': config.ezpayBe.apiKey,
          'x-internal-secret-key': config.ezpayBe.apiKey,
        },
        timeout: 20000,
      }),
    );

    return response.data.data;
  }
}

