import { Injectable, Logger } from '@nestjs/common';
import { Withdrawal } from '../../common/interfaces/withdrawal.interface';
import { EzpayBeClientService } from './ezpay-be-client.service';
import { IAnalyzeTransferBillResult } from '../bank/interfaces/bank-service.interface';
import { DeviceConfig } from '../device/dto/device-config.dto';
import { IUpdateWithdrawal } from './interface/update-withdrawal';

@Injectable()
export class NotificationService {
  private readonly logger = new Logger('NotificationService');

  constructor(
    private readonly ezpayBeClient: EzpayBeClientService,
  ) {}

  async completeWithdrawal(
    withdrawal: Withdrawal, 
    analysisResult: IAnalyzeTransferBillResult, 
    deviceConfig: DeviceConfig): Promise<void> 
  {
    this.ezpayBeClient
      .completeWithdrawal(withdrawal._id, {
        analyzedStatus: analysisResult.analyzedStatus,
        sourceAccountNo: deviceConfig.accountNo,
        sourceBankCode: deviceConfig.bankCode,
        sourceAccountName: deviceConfig.accountName,
      }).catch((error) => {
        this.logger.error(`[${withdrawal.withdrawalCode}] - Error updating withdrawal status for ${withdrawal._id} - ${error.stack}`);
      });

    this.ezpayBeClient
      .uploadReceipt(withdrawal._id, analysisResult.rawPath)
      .catch((error) => {
        this.logger.error(`[${withdrawal.withdrawalCode}] - Error uploading receipt for ${withdrawal._id} - ${error.stack}`);
      });
  }

  updateWithdrawal(id: string, data: IUpdateWithdrawal) {
    return this.ezpayBeClient.updateWithdrawal(id, data);
  }

  uploadReceipt(id: string, receiptPath: string) {
    return this.ezpayBeClient.uploadReceipt(id, receiptPath);
  }
}

