import Redis from 'ioredis';
import { config } from '../constants/config';

const redis = new Redis({
  host: config.redis.host,
  port: config.redis.port,
  password: config.redis.password,
  db: config.redis.db,
});

export const withCronLock = async (key: string, ttlMs: number, fn: () => Promise<void>) => {
  const lockVal = `${process.pid}:${Date.now()}`;
  const ok = await redis.set(key, lockVal, 'PX', ttlMs, 'NX');
  if (!ok) return;

  try {
    await fn();
  } finally {
    // release best-effort: ch xo nu vn l ch lock
    const lua = `
      if redis.call("GET", KEYS[1]) == ARGV[1] then
        return redis.call("DEL", KEYS[1])
      end
      return 0
    `;
    await redis.eval(lua, 1, key, lockVal);
  }
}