import { Injectable, Logger } from '@nestjs/common';
import { readFile } from 'fs/promises';
import { OCRWorkerPoolService } from './ocr-worker-pool.service';
import { ImagePreprocessingService, ROI, PreprocessOptions } from './image-preprocessing.service';
import { normalizeBankAccountName } from '@/common/utils/string';
import { config, IConfig } from '@/common/constants/config';

// Re-export types for consumers
export { ROI, PreprocessOptions } from './image-preprocessing.service';

export interface OCRResult {
  text: string;
  confidence: number;
  processingTime: number;
  timingBreakdown: {
    decodeImage: number;
    preprocess: number;
    ocrRecognize: number;
    postprocess: number;
  };
}

export type FieldType = 'singleLine' | 'block' | 'full';

// Default ROI config for fallback
const DEFAULT_ROI_CONFIG = {
  crop: { x: 0, y: 0, width: 1, height: 1 },
  psm: 6,
  charWhitelist: undefined,
  maxWidth: 1200,
};

@Injectable()
export class OCRService {
  private readonly logger = new Logger(OCRService.name);
  private readonly ocrConfig: Pick<IConfig['ocr'], 'poolSize' | 'lang' | 'rois'>;

  constructor(
    private readonly workerPoolService: OCRWorkerPoolService,
    private readonly imagePreprocessingService: ImagePreprocessingService,
  ) {
    this.ocrConfig = {
      poolSize: config.ocr.poolSize,
      lang: config.ocr.lang,
      rois: {
        full: DEFAULT_ROI_CONFIG,
        singleLine: { ...DEFAULT_ROI_CONFIG, psm: 7 },
        block: { ...DEFAULT_ROI_CONFIG, psm: 6 },
      },
    };
  }

  /**
   * Validate and normalize ROI values
   */
  private validateROI(roi: ROI): ROI {
    return {
      x: Math.max(0, Math.min(1, roi.x || 0)),
      y: Math.max(0, Math.min(1, roi.y || 0)),
      width: Math.max(0.01, Math.min(1, roi.width || 1)),
      height: Math.max(0.01, Math.min(1, roi.height || 1)),
    };
  }

  /**
   * Get ROI config for a field type with proper fallback
   */
  private getROIConfig(fieldType: FieldType) {
    const config = this.ocrConfig.rois?.[fieldType]
      || this.ocrConfig.rois?.full
      || DEFAULT_ROI_CONFIG;

    return {
      crop: config.crop || DEFAULT_ROI_CONFIG.crop,
      psm: config.psm ?? DEFAULT_ROI_CONFIG.psm,
      charWhitelist: config.charWhitelist,
      maxWidth: config.maxWidth ?? DEFAULT_ROI_CONFIG.maxWidth,
    };
  }

  /**
   * Recognize text from image file path
   */
  async recognizeFromFile(
    imagePath: string,
    options: {
      fieldType?: FieldType;
      customROI?: ROI;
      language?: string;
      /** Image preprocessing options */
      preprocess?: PreprocessOptions;
    } = {},
  ): Promise<OCRResult | null> {
    const totalStartTime = Date.now();
    const timingBreakdown = {
      decodeImage: 0,
      preprocess: 0,
      ocrRecognize: 0,
      postprocess: 0,
    };

    try {
      // Step 1: Decode/Load image
      const decodeStart = Date.now();
      const imageBuffer = await readFile(imagePath);
      timingBreakdown.decodeImage = Date.now() - decodeStart;

      // Step 2: Get ROI config based on field type
      const fieldType = options.fieldType || 'full';
      const roiConfig = this.getROIConfig(fieldType);
      const roi = this.validateROI(options.customROI || roiConfig.crop);

      // Step 3: Crop and preprocess
      const preprocessStart = Date.now();
      const preprocessResult = await this.imagePreprocessingService.preprocess(
        imageBuffer,
        roi,
        roiConfig.maxWidth,
        options.preprocess, // Pass preprocessing options
      );
      timingBreakdown.preprocess = Date.now() - preprocessStart;

      // Step 4: OCR recognize
      const ocrStart = Date.now();
      const ocrResult = await this.workerPoolService.recognize(
        preprocessResult.buffer,
        {
          psm: roiConfig.psm,
          charWhitelist: roiConfig.charWhitelist,
        },
      );
      timingBreakdown.ocrRecognize = Date.now() - ocrStart;

      // Step 5: Postprocess (trim and normalize)
      const postprocessStart = Date.now();
      const processedText = this.postprocessText(ocrResult.text);
      timingBreakdown.postprocess = Date.now() - postprocessStart;

      const totalProcessingTime = Date.now() - totalStartTime;

      // Log timing breakdown
      this.logTimingBreakdown(
        imagePath,
        fieldType,
        totalProcessingTime,
        timingBreakdown,
        ocrResult.confidence,
      );

      return {
        text: processedText,
        confidence: ocrResult.confidence,
        processingTime: totalProcessingTime,
        timingBreakdown,
      };
    } catch (error) {
      this.logger.error(`OCR error for ${imagePath}: ${error.message}`, error.stack);
      return null;
    }
  }

  /**
   * Recognize text from image buffer
   */
  async recognizeFromBuffer(
    imageBuffer: Buffer,
    options: {
      fieldType?: FieldType;
      customROI?: ROI;
      language?: string;
      /** Image preprocessing options */
      preprocess?: PreprocessOptions;
    } = {},
  ): Promise<OCRResult | null> {
    const totalStartTime = Date.now();
    const timingBreakdown = {
      decodeImage: 0,
      preprocess: 0,
      ocrRecognize: 0,
      postprocess: 0,
    };

    try {
      // Step 1: Image is already decoded (buffer provided)
      timingBreakdown.decodeImage = 0;

      // Step 2: Get ROI config based on field type
      const fieldType = options.fieldType || 'full';
      const roiConfig = this.getROIConfig(fieldType);
      const roi = this.validateROI(options.customROI || roiConfig.crop);

      // Step 3: Crop and preprocess
      const preprocessStart = Date.now();
      const preprocessResult = await this.imagePreprocessingService.preprocess(
        imageBuffer,
        roi,
        roiConfig.maxWidth,
        options.preprocess, // Pass preprocessing options
      );
      timingBreakdown.preprocess = Date.now() - preprocessStart;

      // Step 4: OCR recognize
      const ocrStart = Date.now();
      const ocrResult = await this.workerPoolService.recognize(
        preprocessResult.buffer,
        {
          psm: roiConfig.psm,
          charWhitelist: roiConfig.charWhitelist,
        },
      );
      timingBreakdown.ocrRecognize = Date.now() - ocrStart;

      // Step 5: Postprocess
      const postprocessStart = Date.now();
      const processedText = this.postprocessText(ocrResult.text);
      timingBreakdown.postprocess = Date.now() - postprocessStart;

      const totalProcessingTime = Date.now() - totalStartTime;

      // Log timing breakdown
      this.logTimingBreakdown(
        'buffer',
        fieldType,
        totalProcessingTime,
        timingBreakdown,
        ocrResult.confidence,
      );

      return {
        text: processedText,
        confidence: ocrResult.confidence,
        processingTime: totalProcessingTime,
        timingBreakdown,
      };
    } catch (error) {
      this.logger.error(`OCR error from buffer: ${error.message}`, error.stack);
      return null;
    }
  }

  /**
   * Fast screen OCR - search for specific texts
   */
  async fastScreenOCR(
    imagePath: string,
    searchTexts: string[],
    options: {
      fieldType?: FieldType;
      customROI?: ROI;
      preprocess?: PreprocessOptions;
    } = {},
  ): Promise<{
    found: boolean;
    matchedTexts: string[];
    result: OCRResult | null;
  }> {
    const result = await this.recognizeFromFile(imagePath, {
      fieldType: options.fieldType || 'full',
      customROI: options.customROI,
      preprocess: options.preprocess,
    });

    if (!result || !result.text) {
      return {
        found: false,
        matchedTexts: [],
        result,
      };
    }

    const normalizedText = normalizeBankAccountName(result.text);
    const matchedTexts = searchTexts.filter((searchText) =>
      normalizedText.includes(normalizeBankAccountName(searchText)),
    );

    return {
      found: matchedTexts.length > 0,
      matchedTexts,
      result,
    };
  }

  /**
   * Postprocess text: trim, normalize whitespace
   */
  private postprocessText(text: string): string {
    return text
      .trim()
      .replace(/\s+/g, ' ') // Normalize multiple spaces to single space
      .replace(/\n{3,}/g, '\n\n'); // Normalize multiple newlines
  }

  /**
   * Log detailed timing breakdown
   */
  private logTimingBreakdown(
    imageSource: string,
    fieldType: FieldType,
    totalTime: number,
    breakdown: {
      decodeImage: number;
      preprocess: number;
      ocrRecognize: number;
      postprocess: number;
    },
    confidence: number,
  ): void {
    const breakdownStr = [
      `decode=${breakdown.decodeImage}ms`,
      `preprocess=${breakdown.preprocess}ms`,
      `ocr=${breakdown.ocrRecognize}ms`,
      `postprocess=${breakdown.postprocess}ms`,
    ].join(' | ');

    // TODO: Uncomment this when debugging
    // this.logger.log(
    //   `OCR [${fieldType}] ${imageSource}: ` +
    //   `total=${totalTime}ms (${breakdownStr}) | ` +
    //   `confidence=${confidence.toFixed(2)}%`,
    // );

    // Warn if total time exceeds target (1s for p95)
    if (totalTime > 1000) {
      this.logger.warn(
        ` OCR latency exceeded target: ${totalTime}ms > 1000ms (p95 target)`,
      );
    }
  }
}

