import { Injectable, Logger, OnModuleInit, OnModuleDestroy } from '@nestjs/common';
import * as Tesseract from 'tesseract.js';
import { config } from '@/common/constants/config';

interface WorkerInfo {
  id: number;
  worker: Tesseract.Worker;
  inUse: boolean;
  lastUsed: number;
}

interface PendingRequest {
  resolve: (worker: WorkerInfo) => void;
  reject: (error: Error) => void;
  timeout: NodeJS.Timeout;
}

@Injectable()
export class OCRWorkerPoolService implements OnModuleInit, OnModuleDestroy {
  private readonly logger = new Logger('OCRWorkerPoolService');
  private readonly pool: WorkerInfo[] = [];
  private readonly waitingQueue: PendingRequest[] = [];
  private readonly poolSize: number;
  private readonly lang: string;
  private readonly maxWaitTime = 5000; // 5 seconds max wait
  private readonly maxPoolSize = 10; // Maximum workers to prevent resource exhaustion
  private isInitializing = false;
  private isDestroyed = false;
  private isAcquiring = false; // Mutex flag for atomic acquisition
  private initializationPromise: Promise<void> | null = null;

  constructor() {
    // Validate pool size: minimum 1, maximum 10
    this.poolSize = Math.max(1, Math.min(config.ocr.poolSize, this.maxPoolSize));
    this.lang = config.ocr.lang;

    if (config.ocr.poolSize > this.maxPoolSize) {
      this.logger.warn(
        `OCR_POOL_SIZE (${config.ocr.poolSize}) exceeds maximum (${this.maxPoolSize}). Using ${this.maxPoolSize}.`
      );
    }
  }

  async onModuleInit(): Promise<void> {
    await this.warmPool();
  }

  async onModuleDestroy(): Promise<void> {
    this.isDestroyed = true;
    // Reject all pending requests
    this.rejectAllPending(new Error('OCR service is shutting down'));
    await this.terminateAll();
  }

  /**
   * Warm up worker pool during module initialization
   */
  private async warmPool(): Promise<void> {
    if (this.isInitializing) {
      return this.initializationPromise || Promise.resolve();
    }

    this.isInitializing = true;
    this.initializationPromise = this.doWarmPool();

    try {
      await this.initializationPromise;
    } finally {
      this.isInitializing = false;
    }
  }

  private async doWarmPool(): Promise<void> {
    const startTime = Date.now();
    this.logger.log(`Warming up OCR worker pool (size: ${this.poolSize}, lang: ${this.lang})...`);

    const initPromises = Array.from({ length: this.poolSize }, (_, index) =>
      this.createWorker(index),
    );

    try {
      await Promise.all(initPromises);
      const warmupTime = Date.now() - startTime;
      this.logger.log(
        `OCR worker pool warmed up successfully in ${warmupTime}ms. Pool size: ${this.pool.length}`,
      );
    } catch (error) {
      this.logger.error(`Failed to warm up worker pool: ${error.message}`, error.stack);
      // Clean up partially initialized workers
      await this.terminateAll();
      throw error;
    }
  }

  /**
   * Create and initialize a single worker
   */
  private async createWorker(index: number): Promise<void> {
    try {
      const worker = await Tesseract.createWorker(
        this.lang,
        1,
        {
          logger: (m: any) => {
            // Only log errors
            if (m.status === 'error') {
              this.logger.error(`Tesseract worker ${index} error: ${m.message}`);
            }
          },
        },
      );

      this.pool.push({
        id: index,
        worker,
        inUse: false,
        lastUsed: Date.now(),
      });

      this.logger.debug(`Worker ${index} created successfully`);
    } catch (error) {
      this.logger.error(`Failed to create worker ${index}: ${error.message}`);
      throw error;
    }
  }

  /**
   * Acquire a worker from the pool with proper queue management
   */
  private async acquireWorker(): Promise<WorkerInfo> {
    if (this.isDestroyed) {
      throw new Error('OCR service is destroyed');
    }

    // Wait for pool initialization if still warming up
    if (this.isInitializing && this.initializationPromise) {
      await this.initializationPromise;
    }

    // Try to find an available worker (atomic check and set)
    const workerInfo = this.tryAcquireFromPool();
    if (workerInfo) {
      return workerInfo;
    }

    // No available worker, add to waiting queue
    return this.waitForWorker();
  }

  /**
   * Try to acquire a worker from pool with mutex lock
   * This ensures atomic acquisition in concurrent scenarios
   */
  private tryAcquireFromPool(): WorkerInfo | null {
    // Check mutex - if another operation is in progress, return null
    if (this.isAcquiring) {
      return null;
    }

    // Set mutex
    this.isAcquiring = true;

    try {
      for (const workerInfo of this.pool) {
        if (!workerInfo.inUse) {
          workerInfo.inUse = true;
          workerInfo.lastUsed = Date.now();
          return workerInfo;
        }
      }
      return null;
    } finally {
      // Release mutex
      this.isAcquiring = false;
    }
  }

  /**
   * Wait for a worker to become available
   */
  private waitForWorker(): Promise<WorkerInfo> {
    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        // Remove from queue
        const index = this.waitingQueue.findIndex(p => p.resolve === resolve);
        if (index !== -1) {
          this.waitingQueue.splice(index, 1);
        }

        // Create temporary worker as fallback
        this.logger.warn('Timeout waiting for worker, creating temporary worker');
        this.createTemporaryWorker()
          .then(resolve)
          .catch(reject);
      }, this.maxWaitTime);

      this.waitingQueue.push({ resolve, reject, timeout });
    });
  }

  /**
   * Create a temporary worker (not in pool)
   */
  private async createTemporaryWorker(): Promise<WorkerInfo> {
    const tempWorker = await Tesseract.createWorker(this.lang, 1, {
      logger: () => { },
    });
    return {
      id: -1, // Negative ID indicates temporary
      worker: tempWorker,
      inUse: true,
      lastUsed: Date.now(),
    };
  }

  /**
   * Release a worker back to the pool
   */
  private releaseWorker(workerInfo: WorkerInfo): void {
    // Check if there are waiting requests
    if (this.waitingQueue.length > 0) {
      const pending = this.waitingQueue.shift()!;
      clearTimeout(pending.timeout);
      workerInfo.lastUsed = Date.now();
      // Worker stays inUse, just transfer to next requester
      pending.resolve(workerInfo);
    } else {
      workerInfo.inUse = false;
      workerInfo.lastUsed = Date.now();
    }
  }

  /**
   * Reject all pending requests
   */
  private rejectAllPending(error: Error): void {
    while (this.waitingQueue.length > 0) {
      const pending = this.waitingQueue.shift()!;
      clearTimeout(pending.timeout);
      pending.reject(error);
    }
  }

  /**
   * Get a worker and execute OCR with automatic cleanup
   */
  async recognize(
    imageBuffer: Buffer,
    options: {
      psm?: number;
      charWhitelist?: string;
    } = {},
  ): Promise<{ text: string; confidence: number }> {
    const workerInfo = await this.acquireWorker();
    const isTemporary = workerInfo.id < 0;

    const { psm, charWhitelist } = options;
    const hadCustomParams = psm !== undefined || !!charWhitelist;

    try {
      // Set parameters in one call if possible
      const params: Record<string, any> = {};
      if (psm !== undefined) {
        params.tessedit_pageseg_mode = psm;
      }
      if (charWhitelist) {
        params.tessedit_char_whitelist = charWhitelist;
      }

      if (Object.keys(params).length > 0) {
        await workerInfo.worker.setParameters(params);
      }

      // Perform OCR
      const { data } = await workerInfo.worker.recognize(imageBuffer);

      return {
        text: data.text,
        confidence: data.confidence,
      };
    } finally {
      if (isTemporary) {
        // Terminate temporary worker immediately
        try {
          await workerInfo.worker.terminate();
        } catch (error) {
          this.logger.warn(`Failed to terminate temporary worker: ${error.message}`);
        }
      } else {
        // Reset worker parameters if we changed them
        if (hadCustomParams) {
          try {
            await workerInfo.worker.setParameters({
              tessedit_pageseg_mode: 6 as any, // Reset to default PSM 6 (SINGLE_BLOCK)
              tessedit_char_whitelist: '', // Clear whitelist
            });
          } catch (error) {
            this.logger.warn(`Failed to reset worker parameters: ${error.message}`);
          }
        }
        this.releaseWorker(workerInfo);
      }
    }
  }

  /**
   * Get pool statistics for monitoring
   */
  getPoolStats(): { total: number; inUse: number; waiting: number } {
    return {
      total: this.pool.length,
      inUse: this.pool.filter(w => w.inUse).length,
      waiting: this.waitingQueue.length,
    };
  }

  /**
   * Terminate all workers in the pool
   */
  private async terminateAll(): Promise<void> {
    if (this.pool.length === 0) {
      return;
    }

    this.logger.log(`Terminating ${this.pool.length} OCR workers...`);

    const terminatePromises = this.pool.map((workerInfo) =>
      workerInfo.worker
        .terminate()
        .then(() => {
          this.logger.debug(`Worker ${workerInfo.id} terminated`);
        })
        .catch((error) => {
          this.logger.error(`Error terminating worker ${workerInfo.id}: ${error.message}`);
        }),
    );

    await Promise.all(terminatePromises);
    this.pool.length = 0;
    this.logger.log('All OCR workers terminated');
  }
}

