  private async inputPasswordFromConfig(deviceId: string): Promise<void> {
    const config = await this.deviceService.getDeviceConfig(deviceId);
    if (!config || !config.password) {
      throw new Error(`Password not configured for device ${deviceId}`);
    }
    
    // Click password field and wait for UI to be ready
    await this.clickWithTransition(deviceId, VCB_UI_COORDINATES.PASSWORD_FIELD.x, VCB_UI_COORDINATES.PASSWORD_FIELD.y, 1500);

    // Attempt to input password
    await this.wsService.inputText(deviceId, config.password);
    await sleep(1000);
    await this.wsService.pressReturnKey(deviceId);
    await sleep(500);
  }

  private async inputSmartOTPFromConfig(deviceId: string, maxRetries: number = 3): Promise<void> {
    const config = await this.deviceService.getDeviceConfig(deviceId);
    if (!config || !config.smartOTP) {
      throw new Error(`SmartOTP not configured for device ${deviceId}`);
    }
    
    await sleep(500);
    await this.wsService.inputText(deviceId, config.smartOTP);
  }

  async launchApp(deviceId: string) {
    await this.wsService.launchApp(deviceId, this.BUNDLE_ID);
    const doesLaunchAppSuccess = await this.verifyScreenState(deviceId, {
      expectedTexts: ['ng nhp'],
      timeout: 10_000,
      pollInterval: 500,
      roi: VCB_ROI_EXPECTED.LAUNCH_APP,
      preprocess: {
        // increase constrast for image recognition
        grayscale: true,
        enhanceContrast: true
      }
    });

    if (!doesLaunchAppSuccess) {
      throw new WithdrawalProcessingError('Failed to launch VIETCOMBANK app', ErrorType.FORCE_LOGIN_AND_REQUEUE);
    } else {
      this.logger.log(`[${deviceId}] - Get login app screen`);
    }
  }

  /**
   * Login to PG bank
   */
  async login(deviceId: string): Promise<void> {
    // Step 0: Kill app
    await this.killApp(deviceId);
    await sleep(2000);

    // Step 1: Launch app
    await this.launchApp(deviceId);

    await this.verifyScreenState(deviceId, {
      expectedTexts: ['ng nhp'],
      timeout: 20_000,
      pollInterval: 500,
      roi: VCB_ROI_EXPECTED.LAUNCH_APP,
    });

    // Step 2: Input password
    await this.inputPasswordFromConfig(deviceId);

    // Step 3: Click login button
    const doesLoginSuccess = await this.clickAndWaitForScreen(deviceId,
      VCB_UI_COORDINATES.LOGIN_BTN.x,
      VCB_UI_COORDINATES.LOGIN_BTN.y,
      {
        expectedTexts: ['Chc nng a thch'],
        timeout: 20_000,
        pollInterval: 500,
        roi: VCB_ROI_EXPECTED.HOME_SCREEN,
      }
    );

    if (!doesLoginSuccess) {
      throw new WithdrawalProcessingError('Failed to login to VIETCOMBANK', ErrorType.FORCE_LOGIN_AND_REQUEUE);
    } else {
      this.logger.log(`[${deviceId}] - Login successfully`);
    }
  }

