  /**
   * Extract transaction amount t khung mu   cui mn hnh
   * ROI da trn screenshot "Kt qu giao dch" ca VIETCOMBANK
   */
  async extractTransactionAmount(imagePath: string): Promise<number | null> {
    try {
      const result = await this.ocrService.recognizeFromFile(imagePath, {
        fieldType: 'singleLine',
        customROI: {
          x: 0.05,      // 5% from left (c margin nh)
          y: 0.75,      // 75% from top (khong cui white card)
          width: 0.9,   // 90% width (full width tr margin)
          height: 0.12, // 12% height (  cha text + padding)
        },
        preprocess: {
          grayscale: true,        // Chuyn sang grayscale
          enhanceContrast: true,  // Tng contrast cho text trn nn 
        },
      });

      if (!result) {
        this.logger.warn('Failed to extract transaction amount from ROI');
        return null;
      }

      // Clean s tin: ch ly s v du phy/chm
      const amountText = result.text.replace(/[^\d.,]/g, '').replace(/,/g, '');
      const amount = parseFloat(amountText);

      if (isNaN(amount)) {
        this.logger.warn(`Could not parse amount from OCR text: ${result.text}`);
        return null;
      }

      this.logger.log(
        `Extracted transaction amount: ${amount.toLocaleString('vi-VN')} VND ` +
        `(confidence: ${result.confidence.toFixed(2)}%, time: ${result.processingTime}ms)`
      );

      return amount;
    } catch (error) {
      this.logger.error(`Error extracting transaction amount: ${error.message}`, error.stack);
      return null;
    }
  }


