import { Injectable, Logger } from '@nestjs/common';
import { RedisService } from '../../common/services/redis.service';
import { randomUUID } from 'crypto';

// Lua script for atomic unlock (only unlock if lockId matches)
const UNLOCK_SCRIPT = `
if redis.call("GET", KEYS[1]) == ARGV[1] then
  return redis.call("DEL", KEYS[1])
else
  return 0
end
`;

@Injectable()
export class RedisLockService {
  private readonly logger = new Logger('RedisLockService');
  private readonly DEFAULT_LOCK_TTL = 3 * 60 * 1000; // 3 minutes default

  constructor(private readonly redisService: RedisService) {}

  /**
   * Acquire lock for a device (atomic operation using SET NX EX)
   */
  async acquireLock(
    key: string,
    ttl: number = this.DEFAULT_LOCK_TTL
  ): Promise<boolean> {
    try {
      const lockId = randomUUID();
      const ttlSeconds = Math.ceil(ttl / 1000);

      // Use atomic SET NX EX to acquire lock
      // This ensures only one process can acquire the lock
      const client = await this.redisService.getClient();
      const result = await client.set(key, lockId, 'EX', ttlSeconds, 'NX');

      return result === 'OK';
    } catch (error) {
      return false;
    }
  }

  /**
   * Release lock for a device (atomic operation using Lua script)
   */
  async releaseLock(key: string): Promise<void> {
    try {
      const client = await this.redisService.getClient();
      
      // Get current lockId before releasing
      const lockId = await client.get(key);
      
      if (!lockId) {
        return;
      }

      // Use Lua script to atomically check and delete only if lockId matches
      // This prevents releasing lock that was acquired by another process
      const result = await client.eval(
        UNLOCK_SCRIPT,
        1, // number of keys
        key, // KEYS[1] - lock key
        lockId as string // ARGV[1] - lockId to verify
      ) as number;

      if (result === 1) {
        this.logger.log(`Released lock (lockId: ${key}:${lockId})`);
      } else {
        // Lock was already released or changed by another process
        this.logger.warn(`Lock was already released or changed (expected lockId: ${key}:${lockId})`);
      }
    } catch (error) {
      this.logger.error(`Error releasing lock: ${key} ${error.message}`, error.stack);
    }
  }

  /**
   * Get current lock
   */
  async getLock(key: string): Promise<string | null> {
    try {
      const lockId = await this.redisService.get(key);

      return lockId ?? null;
    } catch (error) {
      this.logger.error(`[${key}] Error getting lock: ${error.message}`);
      return null;
    }
  }

  /**
   * Check if lock is acquired
   */
  async isLocked(key: string): Promise<boolean> {
    try {
      const lockId = await this.getLock(key);

      return !!lockId;
    } catch (error) {
      this.logger.error(`[${key}] Error checking lock status: ${error.message}`);
      // On error, assume not locked (fail open)
      return false;
    }
  }
}

