import { Injectable, Logger } from '@nestjs/common';
import { exec } from 'child_process';
import { promisify } from 'util';
import { sleep } from '@/common/utils/time';

const execAsync = promisify(exec);

type RestartParams = {
  processName: string;       // "notepad.exe", "chrome.exe"
  exePath?: string;          // "C:\\Program Files\\Google\\Chrome\\Application\\chrome.exe"
  startTarget?: string;      // "notepad" | "calc" | "https://..." | "C:\\folder"
  gracefulWaitMs?: number;   // default 1200
  forceKillWaitMs?: number;  // default 500
  startDelayMs?: number;     // default 300
  runAsAdmin?: boolean;      // default false - Chy vi quyn Administrator (s hin UAC prompt)
  killAsAdmin?: boolean;     // default false - Kill process vi quyn Administrator nu gp "access denied"
};

@Injectable()
export class WindowsAppGitBashService {
  private readonly logger = new Logger(WindowsAppGitBashService.name);

  async restartApp(params: RestartParams): Promise<void> {
    const {
      processName,
      exePath,
      startTarget,
      gracefulWaitMs = 1200,
      forceKillWaitMs = 500,
      startDelayMs = 300,
      runAsAdmin = false,
      killAsAdmin = false,
    } = params;

    try {
      // 1) ng ng dng mt cch graceful (best effort)
      await this.closeMainWindow(processName).catch(() => {});

      // 2) i mt cht, ri kill cng (best effort)
      await sleep(gracefulWaitMs);
      await this.forceKill(processName, killAsAdmin).catch(() => {});

      // 3) m bo process  tt hon ton
      await sleep(forceKillWaitMs);
      await this.waitUntilNotRunning(processName, 5000).catch(() => {});

      // 4) Khi ng li ng dng
      await sleep(startDelayMs);

      if (exePath) {
        await this.startApp({ exePath, runAsAdmin });
        return;
      }

      if (startTarget) {
        await this.startApp({ startTarget, runAsAdmin });
        return;
      }

      // fallback: start theo base name
      const base = processName.replace(/\.exe$/i, '');
      await this.startApp({ startTarget: base, runAsAdmin });
    } catch (error: any) {
      const errorMsg = error?.message || String(error);
      this.logger.error(`[restart] Failed to restart app: ${errorMsg}`, error?.stack);
      throw error;
    }
  }

  // -----------------------------
  // Windows process management
  // -----------------------------

  /**
   * ng ca s chnh ca ng dng mt cch graceful
   */
  private async closeMainWindow(processName: string): Promise<void> {
    const base = processName.replace(/\.exe$/i, '');
    
    // S dng PowerShell  ng ca s mt cch graceful
    const psCommand = `Get-Process -Name "${this.escapePs(base)}" -ErrorAction SilentlyContinue | ForEach-Object { $_.CloseMainWindow() | Out-Null }`;
    const command = `powershell -NoProfile -ExecutionPolicy Bypass -Command "${this.escapePs(psCommand)}"`;

    // TODO: Uncomment when debugging
    // this.logger.debug(`Graceful close: ${processName}`);
    
    try {
      await execAsync(command, { windowsHide: true });
    } catch (error: any) {
      // Ignore errors nu process khng tn ti
      const errorMsg = error?.message || String(error) || '';
      if (!errorMsg.includes('Cannot find a process')) {
        throw error;
      }
    }
  }

  /**
   * Force kill process bng taskkill hoc PowerShell
   * @param processName - Tn process cn kill
   * @param useAdmin - S dng quyn admin  kill (nu gp "access denied")
   */
  private async forceKill(processName: string, useAdmin: boolean = false): Promise<void> {
    const base = processName.replace(/\.exe$/i, '');
    
    // TODO: Uncomment when debugging
    // this.logger.debug(`Force kill: ${processName}${useAdmin ? ' (with admin)' : ''}`);

    try {
      // Th kill bng PowerShell trc (tt hn vi child processes)
      await this.forceKillWithPowerShell(base, useAdmin);
      // Nu thnh cng, return ngay
      return;
    } catch (error: any) {
      const errorMsg = (error?.message || error?.stderr || String(error) || '').toLowerCase();
      
      // Ignore nu process khng tn ti (khng cn kill na)
      if (
        errorMsg.includes('cannot find a process') ||
        errorMsg.includes('process not found') ||
        errorMsg.includes('cannot find process')
      ) {
        this.logger.debug(`Process not found (expected): ${processName}`);
        return;
      }

      // Nu l "access denied" v cha dng admin, th li vi admin
      if (
        (errorMsg.includes('access is denied') || errorMsg.includes('access denied')) &&
        !useAdmin
      ) {
        this.logger.warn(`Access denied, retrying with admin privileges: ${processName}`);
        try {
          await this.forceKillWithPowerShell(base, true);
          return;
        } catch (retryError: any) {
          const retryMsg = (retryError?.message || retryError?.stderr || String(retryError) || '').toLowerCase();
          
          // Nu retry vi admin vn li, fallback sang taskkill
          if (
            !retryMsg.includes('cannot find a process') &&
            !retryMsg.includes('process not found')
          ) {
            this.logger.debug(`PowerShell kill with admin failed, trying taskkill: ${processName}`);
          } else {
            // Process  khng tn ti
            return;
          }
        }
      }

      // Fallback: th taskkill nu PowerShell fail
      try {
        const shouldUseAdmin = useAdmin || errorMsg.includes('access denied') || errorMsg.includes('access is denied');
        let command: string;
        
        if (shouldUseAdmin) {
          // S dng PowerShell  chy taskkill vi quyn admin
          // Escape process name ng cch cho ArgumentList
          const escapedProcessName = processName.replace(/"/g, '`"');
          // S dng single quotes trong PowerShell  trnh escape phc tp
          const psCommand = `Start-Process taskkill -ArgumentList '/F','/IM','${escapedProcessName}','/T' -Verb RunAs -Wait -WindowStyle Hidden`;
          // Escape cho command line
          command = `powershell -NoProfile -ExecutionPolicy Bypass -Command "${psCommand.replace(/"/g, '`"').replace(/\$/g, '`$')}"`;
        } else {
          command = `taskkill /F /IM "${processName}" /T`;
        }
        
        await execAsync(command, { windowsHide: true });
      } catch (taskkillError: any) {
        const taskkillMsg = (taskkillError?.message || taskkillError?.stderr || String(taskkillError) || '').toLowerCase();
        
        // Ignore nu process khng tn ti
        if (
          taskkillMsg.includes('not found') ||
          taskkillMsg.includes('no tasks are running') ||
          taskkillMsg.includes('the system cannot find')
        ) {
          this.logger.debug(`Process not found (expected): ${processName}`);
          return;
        }

        // Nu vn l "access denied", log warning nhng khng throw (best effort)
        if (taskkillMsg.includes('access is denied') || taskkillMsg.includes('access denied')) {
          this.logger.warn(`Cannot kill process (access denied): ${processName}. Process may require administrator privileges to terminate.`);
          return;
        }

        // Log warning cho cc li khc nhng khng throw (best effort)
        this.logger.warn(`Kill process warning: ${taskkillMsg}`);
      }
    }
  }

  /**
   * Kill process bng PowerShell Stop-Process (x l child processes tt hn)
   */
  private async forceKillWithPowerShell(processBaseName: string, useAdmin: boolean = false): Promise<void> {
    // S dng PowerShell Stop-Process vi -Force  kill tt c child processes
    // -Force s kill process ngay c khi c child processes
    const escapedName = processBaseName.replace(/'/g, "''");
    const killCommand = `Get-Process -Name '${escapedName}' -ErrorAction SilentlyContinue | Stop-Process -Force -ErrorAction Stop`;
    
    let command: string;
    
    if (useAdmin) {
      // S dng Start-Process vi RunAs  c quyn admin
      // Escaping: single quotes trong PowerShell argument list
      const escapedKillCommand = killCommand.replace(/'/g, "''").replace(/"/g, '`"');
      // S dng double quotes bn ngoi v escape ng cch
      const psCommand = `Start-Process powershell -ArgumentList '-NoProfile','-ExecutionPolicy','Bypass','-Command',\"${escapedKillCommand}\" -Verb RunAs -Wait -WindowStyle Hidden`;
      command = `powershell -NoProfile -ExecutionPolicy Bypass -Command "${psCommand.replace(/"/g, '`"').replace(/\$/g, '`$')}"`;
    } else {
      // Dng single quotes trong PowerShell  trnh escape phc tp
      command = `powershell -NoProfile -ExecutionPolicy Bypass -Command "${killCommand.replace(/"/g, '`"').replace(/\$/g, '`$')}"`;
    }

    try {
      await execAsync(command, { windowsHide: true });
    } catch (error: any) {
      const errorMsg = (error?.message || error?.stderr || String(error) || '').toLowerCase();
      
      // Ignore nu process khng tn ti
      if (
        errorMsg.includes('cannot find a process') ||
        errorMsg.includes('process not found') ||
        errorMsg.includes('cannot find process')
      ) {
        this.logger.debug(`Process not found (expected): ${processBaseName}`);
        return;
      }
      
      throw error; // Re-throw  caller x l
    }
  }

  /**
   * Kim tra xem process c ang chy khng
   */
  private async isRunning(processName: string): Promise<boolean> {
    try {
      const command = `tasklist /FI "IMAGENAME eq ${processName}" /FO CSV /NH`;
      const { stdout } = await execAsync(command, { windowsHide: true });
      
      const output = stdout.toLowerCase().trim();
      if (!output || output.includes('no tasks') || output === 'info:') {
        return false;
      }
      
      return output.includes(processName.toLowerCase());
    } catch (error) {
      // Nu c li khi check, coi nh khng chy
      return false;
    }
  }

  /**
   * i cho n khi process khng cn chy
   */
  private async waitUntilNotRunning(processName: string, timeoutMs: number): Promise<void> {
    const start = Date.now();
    
    while (Date.now() - start < timeoutMs) {
      const running = await this.isRunning(processName);
      if (!running) {
        return;
      }
      await sleep(250);
    }
    
    this.logger.warn(`Process still running after ${timeoutMs}ms: ${processName}`);
  }

  /**
   * Khi ng ng dng
   * @param args - Tham s khi ng ng dng
   * @param args.exePath - ng dn y  n file exe
   * @param args.startTarget - Target  khi ng (exe name, URL, file path)
   * @param args.runAsAdmin - Chy vi quyn Administrator (hin UAC prompt)
   */
  private async startApp(args: { exePath?: string; startTarget?: string; runAsAdmin?: boolean }): Promise<void> {
    const target = args.exePath ?? args.startTarget ?? '';
    const runAsAdmin = args.runAsAdmin ?? false;
    
    if (!target) {
      throw new Error('Start target is empty');
    }

    // TODO: Uncomment when debugging
    // const adminText = runAsAdmin ? ' as Administrator' : '';
    // this.logger.debug(`[restart] Starting app${adminText}: ${target}`);

    try {
      if (runAsAdmin) {
        // S dng PowerShell Start-Process vi -Verb RunAs  chy vi quyn admin
        // S hin th UAC prompt cho ngi dng
        // S dng single quotes trong PowerShell  trnh phi escape c bit
        const escapedTarget = target.replace(/'/g, "''"); // Escape single quotes trong PowerShell
        const psCommand = `Start-Process -FilePath '${escapedTarget}' -Verb RunAs`;
        // Escape double quotes v $ cho command line
        const escapedCommand = psCommand.replace(/"/g, '`"').replace(/\$/g, '`$');
        const command = `powershell -NoProfile -ExecutionPolicy Bypass -Command "${escapedCommand}"`;
        
        await execAsync(command);
        this.logger.log(`App started with Administrator privileges: ${target}`);
      } else {
        // S dng start command ca Windows  m ng dng bnh thng
        // start ""  khng hin th title ca command prompt
        const command = `start "" "${target}"`;
        await execAsync(command, { windowsHide: true });

        this.logger.log(`App started successfully: ${target}`);
      }
      
      // i mt cht  ng dng khi ng
      await sleep(500);
      
    } catch (error: any) {
      const errorMsg = error?.message || String(error);
      this.logger.error(`Failed to start app: ${target}`, errorMsg);
      throw error;
    }
  }

  // -----------------------------
  // Utils
  // -----------------------------

  private escapePs(value: string): string {
    // Escape " v $ trong PowerShell string
    return value.replace(/"/g, '`"').replace(/\$/g, '`$');
  }
}
