import { Global, Module } from '@nestjs/common';
import { HCWebSocketService } from './hc-websocket.service';
import { PandaWebSocketService } from './panda-websocket.service';
import { WEBSOCKET_SERVICE } from './constants/websocket';
import { config } from '@/common/constants/config';

@Global()
@Module({
  providers: [
    {
      provide: WEBSOCKET_SERVICE,
      useClass: config.boxType === 'PANDA' ? PandaWebSocketService : HCWebSocketService,
    }
  ],
  exports: [WEBSOCKET_SERVICE],
})
export class WebSocketModule {}

