/**
 * Screen verification configuration
 */
export interface ScreenVerificationConfig {
  // Text markers  identify mn hnh (dng OCR)
  expectedTexts?: string[];
  // Text markers khng c xut hin ( verify  ri mn hnh c)
  unexpectedTexts?: string[];
  // Timeout ti a  i mn hnh xut hin (ms)
  timeout?: number;
  // Polling interval - check mi X ms
  pollInterval?: number;
  // ROI  ch OCR mt vng c th (optional - nu khng c s OCR full screen)
  roi?: ROI;
  // Field type cho OCR (singleLine, block, full)
  fieldType?: 'singleLine' | 'block' | 'full';
  // Preprocessing options
  preprocess?: {
    grayscale?: boolean;
    enhanceContrast?: boolean;
  };
}

/**
 * Base abstract class for all bank services
 * Provides common functionality and enforces interface implementation
 */
@Injectable()
export abstract class BaseBankService implements IBankService {
  protected readonly logger: Logger;
  protected abstract readonly BANK_CODE: string;
  protected abstract readonly BUNDLE_ID: string;

  // Static flags  trnh race condition khi nhiu class cng khi to
  protected readonly baseTransactionFolderPath = 'C:/EasyData';
  protected readonly baseTransactionExecutionFolder = 'C:/EasyTrx';
  protected readonly baseBalanceFolder = 'C:/EasyBalance';
  private static folderCreationInProgress = false;
  private static folderCreated = false;

  constructor(
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
    protected readonly deviceService: DeviceService,
    protected ocrService: OCRService,
    protected readonly imagePreprocessingService: ImagePreprocessingService,
  ) {
    this.logger = new Logger('BaseBankService');
    BaseBankService.createTransactionFolder(this.baseTransactionFolderPath, this.logger);
    BaseBankService.createTransactionFolder(this.baseTransactionExecutionFolder, this.logger);
  }

  /**
   * Kim tra v to base folder nu cha tn ti
   * S dng static method  trnh race condition khi nhiu class cng khi to
   */
  private static createTransactionFolder(folderPath: string, logger: Logger): void {
    // Nu  to ri th khng cn lm g
    if (BaseBankService.folderCreated) {
      return;
    }

    // Nu ang c process khc ang to folder, i mt cht ri check li
    if (BaseBankService.folderCreationInProgress) {
      // i ti a 100ms, check mi 10ms
      let attempts = 0;
      const maxAttempts = 10;
      while (BaseBankService.folderCreationInProgress && attempts < maxAttempts) {
        attempts++;
        // Synchronous wait (ch dng trong constructor, khng block event loop lu)
        const start = Date.now();
        while (Date.now() - start < 10) {
          // Busy wait 10ms
        }
      }
      // Sau khi i, check li xem  to cha
      if (BaseBankService.folderCreated || existsSync(folderPath)) {
        BaseBankService.folderCreated = true;
        return;
      }
    }

    try {
      // nh du ang to folder
      BaseBankService.folderCreationInProgress = true;

      // Kim tra li mt ln na (double-check pattern)
      if (!existsSync(folderPath)) {
        mkdirSync(folderPath, { recursive: true });
        logger.log(`Created base folder: ${folderPath}`);
      } else {
        logger.debug(`Base folder already exists: ${folderPath}`);
      }

      // nh du  to xong
      BaseBankService.folderCreated = true;
    } catch (error) {
      // Nu li l "folder already exists" th khng sao (c th xy ra trong race condition)
      if ((error as NodeJS.ErrnoException).code !== 'EEXIST') {
        logger.error(`Failed to create base folder ${folderPath}: ${error.message}`);
      } else {
        // Folder  tn ti (c th do process khc  to), nh du l  to
        BaseBankService.folderCreated = true;
        logger.debug(`Base folder already exists (created by another process): ${folderPath}`);
      }
    } finally {
      // Lun reset flag
      BaseBankService.folderCreationInProgress = false;
    }
  }

  getBankCode(): string {
    return this.BANK_CODE;
  }

  async killApp(deviceId: string): Promise<void> {
    this.logger.log(`[${deviceId}] Killing ${this.BANK_CODE} app`);
    await this.wsService.killApp(deviceId, this.BUNDLE_ID);
  }

  /**
   * Helper method for clicking with transition time and logging
   */
  async clickWithTransition(
    deviceId: string,
    x: number,
    y: number,
    transitionTime: number,
    log: string = ''
  ): Promise<void> {
    await this.wsService.click(deviceId, x, y, 0.2);
    if (log) {
      this.logger.log(log);
    }
    // Wait for screen transition
    await sleep(transitionTime);
  }

  // Helper function  cleanup folder
  private cleanupFolder = async (dir: string, deviceId: string) => {
    if (existsSync(dir)) {
      try {
        await rm(dir, { recursive: true, force: true });
        // this.logger.debug(`[${deviceId}] Cleaned up temp folder: ${tempDir}`);
      } catch (cleanupError) {
        // Ignore cleanup errors
        this.logger.debug(`[${deviceId}] Failed to cleanup folder: ${cleanupError.message}`);
      }
    }
  };

  /**
   * Verify screen state bng cch capture v check text qua Local OCR
   * Flow:
   * 1. Capture screen  lu vo folder tm (unique mi ln chy)
   * 2. Local OCR c text t file (nhanh, khng cn API)
   * 3. So snh vi expected/unexpected texts
   * 4. Xa c folder tm sau khi OCR
   * 5. Return true nu match, false nu khng
   */
  protected async verifyScreenState(
    deviceId: string,
    config: ScreenVerificationConfig
  ): Promise<boolean> {
    const {
      expectedTexts = [],
      unexpectedTexts = [],
      timeout = 30000,
      pollInterval = 500,
      roi,
      fieldType = 'full',
      preprocess,
    } = config;

    const startTime = Date.now();
    let attemptCount = 0;

    // To folder tm cho mi ln verifyScreenState (1 device ch lm 1 action nn khng c conflict)
    const tempDir = join(this.baseTransactionExecutionFolder, `scan_${deviceId}`);

    // Log vi ROI info nu c
    const roiInfo = roi
      ? `ROI: x=${roi.x}, y=${roi.y}, w=${roi.width}, h=${roi.height}`
      : 'Full screen';

    this.logger.debug(
      `[${deviceId}] Starting screen verification. ` +
      `Expected: [${expectedTexts.join(', ')}], ` +
      `Unexpected: [${unexpectedTexts.join(', ')}], ` +
      `${roiInfo}, ` +
      `Timeout: ${timeout}ms, Poll: ${pollInterval}ms`
    );

    try {
      // Xa folder c nu tn ti (t ln chy trc b crash/timeout)
      await this.cleanupFolder(tempDir, deviceId);

      // To folder tm mi
      await mkdir(tempDir, { recursive: true });

      while (Date.now() - startTime < timeout) {
        attemptCount++;
        let tempScreenshotPath: string | null = null;

        try {

          // Ghi nhn thi im trc khi capture  ch ly file mi
          const beforeCaptureTime = Date.now();

          // Capture screen vo folder (file s c gen vi UUID.png)
          await this.wsService.captureScreen(deviceId, {
            folderPath: tempDir
          });

          // Bc 2: i v tm file mi nht (retry nhiu ln  m bo file c lu)
          let latestFile: string | null = null;
          const maxFileWaitAttempts = 5;
          let fileWaitAttempt = 0;

          while (fileWaitAttempt < maxFileWaitAttempts && !latestFile) {
            fileWaitAttempt++;
            await sleep(500); // i 500ms mi ln

            try {
              if (!existsSync(tempDir)) {
                continue;
              }

              const files = await readdir(tempDir);
              const pngFiles = files.filter(f => f.endsWith('.png'));

              if (pngFiles.length === 0) {
                continue; // Cha c file, i tip
              }

              // Tm file c mtime mi nht (ch ly file c to sau capture)
              let latestMtime = 0;
              const now = Date.now();
              for (const file of pngFiles) {
                const filePath = join(tempDir, file);
                try {
                  const fileStat = await stat(filePath);
                  if (fileStat.size === 0) {
                    continue;
                  }

                  // Ch ly file c to sau thi im capture
                  if (fileStat.mtimeMs >= beforeCaptureTime - 2000) {
                    if (fileStat.mtimeMs > latestMtime) {
                      latestMtime = fileStat.mtimeMs;
                      latestFile = filePath;
                    }
                  }
                } catch (statError) {
                  continue;
                }
              }
            } catch (readdirError) {
              // Folder cha tn ti hoc khng c c, i tip
              continue;
            }
          }

          if (!latestFile) {
            // this.logger.debug(`[${deviceId}] No screenshot files found in folder after ${fileWaitAttempt} attempts, retrying capture...`);
            await sleep(pollInterval);
            continue;
          }

          tempScreenshotPath = latestFile;

          // Bc 4: Gi OCR Service vi ROI nu c
          const allSearchTexts = [...expectedTexts, ...unexpectedTexts];
          const ocrResult = await this.ocrService.fastScreenOCR(
            tempScreenshotPath,
            allSearchTexts,
            {
              fieldType: roi ? 'singleLine' : fieldType,
              customROI: roi,
              preprocess: preprocess,
            }
          );

          // Bc 5: X l kt qu OCR
          if (!ocrResult || !ocrResult.found) {
            await sleep(pollInterval);
            continue;
          }

          // Bc 6: So snh vi expected texts - Check if expected texts are in matchedTexts
          const hasExpectedTexts = expectedTexts.length === 0 || expectedTexts.some(text =>
            ocrResult.matchedTexts.includes(text)
          );

          // Bc 7: Check unexpected texts (khng c xut hin)
          const hasUnexpectedTexts = unexpectedTexts.some(text =>
            ocrResult.matchedTexts.includes(text)
          );

          // Bc 8: Verify kt qu
          if (hasExpectedTexts || hasUnexpectedTexts) {
            // Xa folder khi success
            await this.cleanupFolder(tempDir, deviceId);

            const elapsed = Date.now() - startTime;
            const ocrTime = ocrResult.result?.processingTime || 0;
            const roiStr = roi ? ` (ROI: ${roi.x},${roi.y} ${roi.width}x${roi.height})` : '';
            this.logger.log(
              `[${deviceId}] Screen verified successfully after ${elapsed}ms ` +
              `(${attemptCount} attempts, OCR: ${ocrTime}ms)${roiStr}. ` +
              `Matched: [${ocrResult.matchedTexts.join(', ')}]`
            );
            return true;
          }

          // Cha ng mn hnh, log debug
          if (attemptCount % 5 === 0) {
            this.logger.debug(
              `[${deviceId}] Screen not matched yet (attempt ${attemptCount}). ` +
              `Matched texts: [${ocrResult.matchedTexts.join(', ')}], ` +
              `Expected: [${expectedTexts.join(', ')}]`
            );
          }

          // Xa file screenshot sau mi attempt  trnh tch t
          if (tempScreenshotPath) {
            try {
              await unlink(tempScreenshotPath);
            } catch {
              // Ignore unlink errors
            }
          }

          // i trc khi th li
          await sleep(pollInterval);

        } catch (error) {
          this.logger.debug(
            `[${deviceId}] Error verifying screen (attempt ${attemptCount}): ${error.message}, retrying...`
          );
          await sleep(pollInterval);
        }
      }

      // Timeout - xa folder trc khi return
      await this.cleanupFolder(tempDir, deviceId);

      const elapsed = Date.now() - startTime;
      this.logger.warn(
        `[${deviceId}] Screen verification timeout after ${elapsed}ms ` +
        `(${attemptCount} attempts). Expected: [${expectedTexts.join(', ')}]`
      );
      return false;

    } catch (error) {
      // Cleanup folder nu c li nghim trng
      await this.cleanupFolder(tempDir, deviceId);
      throw error;
    }
  }

  async getLatestImagePath(
    folderPath: string,
    timeout: number = 10000,
    pollInterval: number = 500
  ): Promise<string | null> {
    if (!folderPath) {
      this.logger.warn('Folder path is null or empty');
      return null;
    }

    const startTime = Date.now();
    let attemptCount = 0;

    this.logger.debug(
      `Scanning folder for bill image: ${folderPath}, timeout: ${timeout}ms, pollInterval: ${pollInterval}ms`
    );

    while (Date.now() - startTime < timeout) {
      attemptCount++;

      try {
        // Kim tra th mc c tn ti khng
        if (!existsSync(folderPath)) {
          await sleep(pollInterval);
          continue;
        }

        const files = await readdir(folderPath);

        if (files.length === 0) {
          // TODO: Uncomment this when debugging
          // this.logger.debug(`No files found in folder: ${folderPath}, retrying... (attempt ${attemptCount})`);
          await sleep(pollInterval);
          continue;
        }

        // Ly thng tin tt c cc file
        const filesWithStat = await Promise.all(
          files.map(async file => {
            try {
              const fullPath = join(folderPath, file);
              const s = await stat(fullPath);
              return { fullPath, mtime: s.mtimeMs, size: s.size };
            } catch (statError) {
              // Skip files that can't be stat'd
              return null;
            }
          })
        );

        // Lc cc file hp l (size > 0)
        const validFiles = filesWithStat.filter(
          (f): f is { fullPath: string; mtime: number; size: number } => 
            f !== null && f.size > 0
        );

        if (validFiles.length === 0) {
          await sleep(pollInterval);
          continue;
        }

        // Sp xp theo thi gian modified mi nht
        validFiles.sort((a, b) => b.mtime - a.mtime);

        const latestFile = validFiles[0].fullPath;
        const elapsed = Date.now() - startTime;
        this.logger.log(
          `Found latest bill image: ${latestFile} after ${elapsed}ms (${attemptCount} attempts)`
        );

        return latestFile;
      } catch (error) {
        this.logger.debug(
          `Error scanning folder ${folderPath} (attempt ${attemptCount}): ${error.message}, retrying...`
        );
        await sleep(pollInterval);
      }
    }

    // Timeout - khng tm thy file
    const elapsed = Date.now() - startTime;
    this.logger.warn(
      `Timeout scanning folder for bill image: ${folderPath} after ${elapsed}ms (${attemptCount} attempts)`
    );
    return null;
  }

  async detectScreenByHint(
    deviceId: string,
    screenHints: Array<{
      name: string;
      hint: string
    }>,
    config: Pick<ScreenVerificationConfig, 'timeout' | 'pollInterval' | 'roi' | 'preprocess'>
  ): Promise<{
    name: string;
    hint: string;
  } | null> {
    const tempDir = join(this.baseTransactionExecutionFolder, `scan_${deviceId}`);
    const screenHintProcessed = screenHints.map(screenHint => {
      return {
        name: screenHint.name,
        hint: removeVietnameseTones(screenHint.hint),
      }
    });


    const startTime = Date.now();
    let attemptCount = 0
    const { roi, timeout, pollInterval } = config;

    // Log vi ROI info nu c
    const roiInfo = roi
      ? `ROI: x=${roi.x}, y=${roi.y}, w=${roi.width}, h=${roi.height}`
      : 'Full screen';

    this.logger.debug(
      `[${deviceId}] Start detecting screen. ` +
      `Screen: [${screenHints.map(screenHint => screenHint.name).join(' or ')}], ` +
      `${roiInfo}, ` +
      `Timeout: ${timeout}ms, Poll: ${pollInterval}ms`
    );

    try {
      // Xa folder c nu tn ti (t ln chy trc b crash/timeout)
      await this.cleanupFolder(tempDir, deviceId);

      // To folder tm mi
      await mkdir(tempDir, { recursive: true });

      while (Date.now() - startTime < timeout) {
        attemptCount++;
        let tempScreenshotPath: string | null = null;

        try {

          // Ghi nhn thi im trc khi capture  ch ly file mi
          const beforeCaptureTime = Date.now();

          // Capture screen vo folder (file s c gen vi UUID.png)
          await this.wsService.captureScreen(deviceId, {
            folderPath: tempDir
          });

          // Bc 2: i v tm file mi nht (retry nhiu ln  m bo file c lu)
          let latestFile: string | null = null;
          const maxFileWaitAttempts = 5;
          let fileWaitAttempt = 0;

          while (fileWaitAttempt < maxFileWaitAttempts && !latestFile) {
            fileWaitAttempt++;
            await sleep(500); // i 500ms mi ln

            try {
              if (!existsSync(tempDir)) {
                continue;
              }

              const files = await readdir(tempDir);
              const pngFiles = files.filter(f => f.endsWith('.png'));

              if (pngFiles.length === 0) {
                continue; // Cha c file, i tip
              }

              // Tm file c mtime mi nht (ch ly file c to sau capture)
              let latestMtime = 0;
              for (const file of pngFiles) {
                const filePath = join(tempDir, file);
                try {
                  const fileStat = await stat(filePath);
                  if (fileStat.size === 0) {
                    continue;
                  }

                  // Ch ly file c to sau thi im capture
                  if (fileStat.mtimeMs >= beforeCaptureTime - 2000) {
                    if (fileStat.mtimeMs > latestMtime) {
                      latestMtime = fileStat.mtimeMs;
                      latestFile = filePath;
                    }
                  }
                } catch (statError) {
                  continue;
                }
              }
            } catch (readdirError) {
              // Folder cha tn ti hoc khng c c, i tip
              continue;
            }
          }

          if (!latestFile) {
            // this.logger.debug(`[${deviceId}] No screenshot files found in folder after ${fileWaitAttempt} attempts, retrying capture...`);
            await sleep(pollInterval);
            continue;
          }

          // Bc 4: Find screen hints
          const imageBuffer = await readFile(latestFile);

          const processedImage = await this.imagePreprocessingService.preprocess(
            imageBuffer, 
            config.roi 
              ?  config.roi 
              : {
                  x: 0,
                  y: 0,
                  width: 1,
                  height: 1,
                },  
            undefined,
            config.preprocess
          );

          const ocrResult = await this.ocrService.recognizeFromBuffer(processedImage.buffer);

          if (!ocrResult.text) {
            await sleep(pollInterval);
            continue;
          }

          const normalizedText = removeVietnameseTones(ocrResult.text);

          const screenHintFound = screenHintProcessed.find(screenHint => normalizedText.includes(screenHint.hint));

          if (!screenHintFound) {
            await sleep(pollInterval);
            continue;
          }

          // Xa file screenshot sau mi attempt  trnh tch t
          if (tempScreenshotPath) {
            try {
              await unlink(tempScreenshotPath);
            } catch {
              // Ignore unlink errors
            }
          }

          return screenHintFound;

        } catch (error) {
          this.logger.debug(
            `[${deviceId}] Error verifying screen (attempt ${attemptCount}): ${error.message}, retrying...`
          );
          await sleep(pollInterval);
        }
      }

      // Timeout - xa folder trc khi return
      await this.cleanupFolder(tempDir, deviceId);

      const elapsed = Date.now() - startTime;
      this.logger.warn(
        `[${deviceId}] Screen verification timeout after ${elapsed}ms ` +
        `(${attemptCount} attempts). Expected: Find [${screenHints.join(' or ')}],`
      );

      return null;

    } catch (error) {
      // Cleanup folder nu c li nghim trng
      await this.cleanupFolder(tempDir, deviceId);
      throw error;
    }
  }

  /**
   * Click v t ng verify mn hnh  chuyn
   * Thay th cho clickWithTransition vi screen verification
   */
  protected async clickAndWaitForScreen(
    deviceId: string,
    x: number,
    y: number,
    screenConfig: ScreenVerificationConfig,
    log?: string
  ): Promise<boolean> {
    // Click
    await this.wsService.click(deviceId, x, y, 0.2);
    if (log) {
      this.logger.log(log);
    }
    
    const verified = await this.verifyScreenState(deviceId, screenConfig);
    
    // TODO: Uncomment this when debugging
    // this.logger.debug(`[${deviceId}] Screen verified after click at (${x}, ${y})`);
    return verified
  }

  /**
   * Swipe v t ng verify mn hnh  chuyn
   * Retry ti a 3 ln nu khng detect c screen, mi ln timeout 1s
   */
  protected async swipeAndWaitForScreen(
    deviceId: string,
    swipeOptions: IHCSwipeOptions | IPandaSwipeOptions,
    screenConfig: ScreenVerificationConfig,
    log?: string
  ): Promise<boolean> {
    const maxRetries = 3;
    const timeoutPerAttempt = 3000 // 1 second

    for (let attempt = 1; attempt <= maxRetries; attempt++) {
      // Swipe
      await this.wsService.swipe(deviceId, swipeOptions);
      await sleep(800);
      if (log && attempt === 1) {
        this.logger.log(log);
      }

      // Verify screen vi timeout 1s cho mi attempt
      const verified = await this.verifyScreenState(deviceId, {
        ...screenConfig,
        timeout: timeoutPerAttempt,
      });

      if (verified) {
        if (attempt > 1) {
          this.logger.log(`[${deviceId}] Screen verified after ${attempt} swipe attempts`);
        }
        return true;
      }

      // Nu khng detect c, capture v OCR  log ra text c detect
      try {
        const tempDir = join(this.baseTransactionExecutionFolder, `scan_${deviceId}_debug`);

        // Xa folder c trc khi to mi (trnh file c b ln)
        if (existsSync(tempDir)) {
          try {
            await rm(tempDir, { recursive: true, force: true });
          } catch { }
        }

        await mkdir(tempDir, { recursive: true });

        const beforeCaptureTime = Date.now();
        await this.wsService.captureScreen(deviceId, {
          folderPath: tempDir
        });

        // i v tm file mi nht (retry  m bo file c lu)
        let latestFile: string | null = null;
        const maxFileWaitAttempts = 5;
        let fileWaitAttempt = 0;

        while (fileWaitAttempt < maxFileWaitAttempts && !latestFile) {
          fileWaitAttempt++;
          await sleep(500);

          try {
            if (!existsSync(tempDir)) {
              continue;
            }

            const files = await readdir(tempDir);
            const pngFiles = files.filter(f => f.endsWith('.png'));

            if (pngFiles.length === 0) {
              continue;
            }

            let latestMtime = 0;
            for (const file of pngFiles) {
              const filePath = join(tempDir, file);
              try {
                const fileStat = await stat(filePath);
                if (fileStat.size > 0 && fileStat.mtimeMs >= beforeCaptureTime - 2000) {
                  if (fileStat.mtimeMs > latestMtime) {
                    latestMtime = fileStat.mtimeMs;
                    latestFile = filePath;
                  }
                }
              } catch { }
            }
          } catch { }
        }

        if (latestFile) {
          const { expectedTexts = [], unexpectedTexts = [], roi, fieldType = 'full', preprocess } = screenConfig;
          const allSearchTexts = [...expectedTexts, ...unexpectedTexts];

          let ocrText = '';
          let matchedTexts: string[] = [];

          // OCR  xem text c detect
          if (allSearchTexts.length > 0) {
            // C searchTexts, dng fastScreenOCR
            const ocrResult = await this.ocrService.fastScreenOCR(
              latestFile,
              allSearchTexts,
              {
                fieldType: roi ? 'singleLine' : fieldType,
                customROI: roi,
                preprocess: preprocess,
              }
            );
            ocrText = ocrResult?.result?.text || '';
            matchedTexts = ocrResult?.matchedTexts || [];
          } else {
            // Khng c searchTexts, OCR full  xem text
            const ocrResult = await this.ocrService.recognizeFromFile(
              latestFile,
              {
                fieldType: roi ? 'singleLine' : fieldType,
                customROI: roi,
                preprocess: preprocess,
              }
            );
            ocrText = ocrResult?.text || '';
          }

          // Log ra text c detect
          const roiStr = roi ? ` ROI: (${roi.x},${roi.y} ${roi.width}x${roi.height})` : '';
          if (ocrText) {
            this.logger.warn(
              `[${deviceId}] Swipe attempt ${attempt}/${maxRetries} - OCR detected text: "${ocrText}"` +
              (matchedTexts.length > 0 ? ` | Matched texts: [${matchedTexts.join(', ')}]` : '') +
              ` | Expected: [${expectedTexts.join(', ') || 'none'}]${roiStr}`
            );
          } else {
            this.logger.warn(
              `[${deviceId}] Swipe attempt ${attempt}/${maxRetries} - OCR found no text${roiStr}`
            );
          }
        } else {
          this.logger.warn(
            `[${deviceId}] Swipe attempt ${attempt}/${maxRetries} - Could not find screenshot file after ${fileWaitAttempt} attempts`
          );
        }

        // Cleanup debug folder
        try {
          await rm(tempDir, { recursive: true, force: true });
        } catch { }
      } catch (debugError) {
        // Log debug errors  bit vn 
        this.logger.debug(`[${deviceId}] Failed to debug OCR: ${debugError.message}`);
      }

      // Nu khng detect c v cha ht retry, log v th li
      if (attempt < maxRetries) {
        this.logger.debug(`[${deviceId}] Screen not detected after swipe (attempt ${attempt}/${maxRetries}), retrying...`);
      }
    }

    // Tt c attempts u fail
    this.logger.warn(`[${deviceId}] Failed to detect screen after ${maxRetries} swipe attempts`);
    return false;
  }

  async createBillFolder(folderPath: string): Promise<void> {
    const path = resolve(this.baseTransactionFolderPath, folderPath);

    await mkdir(path, { recursive: true });
  }

  /**
   * Capture screen of device
   */
  async captureScreen(deviceId: string, options: ICaptureScreenOptions) {
    this.logger.log(`Capturing screen on device ${deviceId} with folderPath: ${options.folderPath}`);

    const path = resolve(this.baseTransactionFolderPath, options.folderPath);

    await this.wsService.captureScreen(deviceId, {
      ...options,
      folderPath: path,
    });

    await sleep(500);
  }

  /**
   * Qut th mc cho n khi timeout v tr v file mi nht
   * @param folderPath ng dn n folder cha nh
   * @param timeout Timeout ti a  i file (ms), mc nh 30000ms (30s)
   * @param pollInterval Khong thi gian gia cc ln qut (ms), mc nh 500ms
   * @returns ng dn file mi nht hoc null nu khng tm thy
   */
  async getBillImagePath(
    folderPath: string,
    timeout: number = 10000,
    pollInterval: number = 500
  ): Promise<string | null> {
    if (!folderPath) {
      this.logger.warn('Folder path is null or empty');
      return null;
    }

    const folder = join(this.baseTransactionFolderPath, folderPath);
    const startTime = Date.now();
    let attemptCount = 0;

    this.logger.debug(
      `Scanning folder for bill image: ${folderPath}, timeout: ${timeout}ms, pollInterval: ${pollInterval}ms`
    );

    while (Date.now() - startTime < timeout) {
      attemptCount++;

      try {
        // Kim tra th mc c tn ti khng
        if (!existsSync(folder)) {
          // TODO: Uncomment this when debugging
          // this.logger.debug(`Folder does not exist yet: ${folderPath}, retrying...`);
          await sleep(pollInterval);
          continue;
        }

        const files = await readdir(folder);

        if (files.length === 0) {
          // TODO: Uncomment this when debugging
          // this.logger.debug(`No files found in folder: ${folderPath}, retrying... (attempt ${attemptCount})`);
          await sleep(pollInterval);
          continue;
        }

        // Ly thng tin tt c cc file
        const filesWithStat = await Promise.all(
          files.map(async file => {
            try {
              const fullPath = join(folder, file);
              const s = await stat(fullPath);
              return { fullPath, mtime: s.mtimeMs, size: s.size };
            } catch (statError) {
              // Skip files that can't be stat'd
              return null;
            }
          })
        );

        // Lc cc file hp l (size > 0)
        const validFiles = filesWithStat.filter(
          (f): f is { fullPath: string; mtime: number; size: number } =>
            f !== null && f.size > 0
        );

        if (validFiles.length === 0) {
          // TODO: Uncomment this when debugging
          // this.logger.debug(`No valid files found in folder: ${folderPath}, retrying... (attempt ${attemptCount})`);
          await sleep(pollInterval);
          continue;
        }

        // Sp xp theo thi gian modified mi nht
        validFiles.sort((a, b) => b.mtime - a.mtime);

        const latestFile = validFiles[0].fullPath;
        const elapsed = Date.now() - startTime;
        this.logger.log(
          `Found latest bill image: ${latestFile} after ${elapsed}ms (${attemptCount} attempts)`
        );

        return latestFile;
      } catch (error) {
        this.logger.debug(
          `Error scanning folder ${folderPath} (attempt ${attemptCount}): ${error.message}, retrying...`
        );
        await sleep(pollInterval);
      }
    }

    // Timeout - khng tm thy file
    const elapsed = Date.now() - startTime;
    this.logger.warn(
      `Timeout scanning folder for bill image: ${folderPath} after ${elapsed}ms (${attemptCount} attempts)`
    );
    return null;
  }

  async analyzeTransferBill(bankCode: string, withdrawal: Partial<Withdrawal>): Promise<IAnalyzeTransferBillResult> {
    const { ROI, expectedTexts } = ANALYZE_BILL_ROI[bankCode];
    const billImage = await this.getBillImagePath(withdrawal.withdrawalCode);

    try {
      // No image found
      if (!billImage) {
        this.logger.warn(`No bill image path ${withdrawal.withdrawalCode}`);

        return {
          rawPath: null,
          analyzedStatus: 'na',
        };
      }

      // const ocrResult = await ocrBillPro(billImage);
      const ocrResult = await this.ocrService.fastScreenOCR(
        billImage,
        expectedTexts,
        {
          fieldType: 'singleLine',
          customROI: ROI,
          preprocess: {
            // increase constrast for image recognition
            grayscale: true,
            enhanceContrast: true
          }
        }
      );

      if (!ocrResult.found) {
        this.logger.warn(`No OCR result found for ${withdrawal.withdrawalCode}`);

        return {
          rawPath: billImage,
          analyzedStatus: 'na'
        }
      }

      return {
        rawPath: billImage,
        analyzedStatus: ocrResult.found ? 'success' : 'unknown',
      };
    } catch (error) {
      this.logger.error(`Error analyzing transfer bill: ${error.message}`);

      return {
        rawPath: billImage,
        analyzedStatus: 'na'
      }
    }
  }

  async createQRCodeAndTransferTo(deviceId: string, withdrawal: Withdrawal) {
    const folderPath = join(this.baseTransactionExecutionFolder, 'qrcode');

    await mkdir(folderPath, { recursive: true });
    const fileName = `qr_${withdrawal.withdrawalCode}.png`;
    const filePath = join(folderPath, fileName);

    await QRCodeToFile(
      filePath,
      withdrawal.transferCode,
      {
        type: 'png',
        width: 228,
        margin: 2
      }
    );

    await sleep(500);

    await this.wsService.transferFile(deviceId, filePath, 1);

    await sleep(500);

    this.logger.log(`[${deviceId}] QR code is created and transferred for withdrawal ${withdrawal.withdrawalId}`);
  }

  protected async extractBalanceFromROI(
    deviceId: string,
    roi: ROI
  ): Promise<number> {
    const tempDir = join(this.baseBalanceFolder, `balance_${deviceId}`);
    
    try {
      // Capture screen to extract balance text
      await sleep(1000);
      await mkdir(tempDir, { recursive: true });
      await this.captureScreen(deviceId, { folderPath: tempDir });
      await sleep(1000);
      
      // Get the first image file in tempDir
      let imagePath: string | null = null;
      const maxFileWaitAttempts = 5;
      let fileWaitAttempt = 0;

      while (fileWaitAttempt < maxFileWaitAttempts && !imagePath) {
        fileWaitAttempt++;
        await sleep(500); // i 500ms mi ln

        try {
          if (!existsSync(tempDir)) {
            continue;
          }

          const files = await readdir(tempDir);
          const pngFiles = files.filter(f => f.endsWith('.png'));

          if (pngFiles.length === 0) {
            continue; // Cha c file, i tip
          }

          // Ly file PNG u tin
          const firstPngFile = pngFiles[0];
          const filePath = join(tempDir, firstPngFile);
          
          // Kim tra file c hp l khng (size > 0)
          try {
            const fileStat = await stat(filePath);
            if (fileStat.size > 0) {
              imagePath = filePath;
            }
          } catch (statError) {
            continue;
          }
        } catch (readdirError) {
          // Folder cha tn ti hoc khng c c, i tip
          continue;
        }
      }
      
      if (!imagePath) {
        this.logger.warn(`[${deviceId}] Failed to capture balance screen`);
        await this.cleanupFolder(tempDir, deviceId);
        return 0;
      }

      // Extract text from ROI using OCR
      const ocrResult = await this.ocrService.recognizeFromFile(imagePath, {
        fieldType: 'singleLine',
        customROI: roi,
        preprocess: {
          grayscale: true,
          enhanceContrast: true,
        },
      });

      if (!ocrResult || !ocrResult.text) {
        this.logger.warn(`[${deviceId}] Failed to extract balance text from ROI`);
        await this.cleanupFolder(tempDir, deviceId);
        return 0;
      }

      // Clean balance text: remove non-numeric characters except dots/commas
      const balanceText = ocrResult.text.replace(/[^\d.,]/g, '').replace(/,/g, '');
      const balance = parseFloat(balanceText);

      if (isNaN(balance)) {
        this.logger.warn(`[${deviceId}] Could not parse balance from OCR text: ${ocrResult.text}`);
        await this.cleanupFolder(tempDir, deviceId);
        return 0;
      }

      this.logger.log(
        `[${deviceId}] Extracted balance: ${balance.toLocaleString('vi-VN')} VND ` +
        `(confidence: ${ocrResult.confidence.toFixed(2)}%, time: ${ocrResult.processingTime}ms)`
      );


      // Cleanup folder sau khi phn tch xong
      await this.cleanupFolder(tempDir, deviceId);

      return balance;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error extracting balance from ROI: ${error.message}`, error.stack);
      // Cleanup folder nu c li
      await this.cleanupFolder(tempDir, deviceId);
      return 0;
    }
  }

  /**
   * Abstract methods - must be implemented by each bank
   */
  abstract login(deviceId: string): Promise<void>;
  abstract launchApp(deviceId: string): Promise<void>;
  abstract executeTransferWithQRCode(withdrawal: Withdrawal, deviceConfig: DeviceConfig): Promise<void>;
  abstract goToHomeScreenFromBill(deviceConfig: DeviceConfig): Promise<void>;
  abstract getBalance(deviceId: string): Promise<number | null>;
}

