import { ErrorType } from "@/modules/withdrawal/withdrawal-errors";
import { IsNotEmpty, IsOptional } from "class-validator";

export class Withdrawal {
  @IsNotEmpty()
  _id: string;
  
  @IsNotEmpty()
  withdrawalId: string;

  @IsNotEmpty()
  withdrawalCode: string;

  @IsNotEmpty()
  bankCode: string;

  @IsNotEmpty()
  beneficiaryAccountNo: string;

  @IsNotEmpty()
  beneficiaryName: string;

  @IsNotEmpty()
  amount: number;
  
  @IsNotEmpty()
  transferCode: string;

  @IsNotEmpty()
  status: string;

  @IsNotEmpty()
  description: string;

  @IsNotEmpty()
  createdAt: string;

  @IsOptional()
  manualRetryCount?: number;

  @IsOptional()
  lastErrorType?: ErrorType;

  @IsOptional()
  lastErrorReason?: string;
}

export interface WithdrawalRequestJob {
  withdrawal: Withdrawal; // Required: withdrawal data passed directly in job
  retryCount?: number;
}

