import { Injectable, Logger, OnModuleDestroy } from '@nestjs/common';
import Redis from 'ioredis';
import { config } from '../constants/config';

@Injectable()
export class RedisService implements OnModuleDestroy {
  private readonly logger = new Logger('RedisService');
  private redisClient: Redis | null = null;

  constructor() {}

  async getClient(): Promise<Redis> {
    if (this.redisClient) {
      return this.redisClient;
    }

    const host = config.redis.host ?? 'localhost';
    const port = config.redis.port ?? 6379;
    const password = config.redis.password ?? undefined;
    const db = config.redis.db ?? 3;

    this.redisClient = new Redis({
      host,
      port,
      password,
      db,
      enableReadyCheck: false,
      maxRetriesPerRequest: null,
      lazyConnect: true,
    });

    this.redisClient.on('error', (err) => {
      this.logger.error('Redis Client Error', err.stack || err.message);
    });

    this.redisClient.on('connect', () => {
      this.logger.log('Redis Client Connected');
    });

    this.redisClient.on('ready', () => {
      this.logger.log('Redis Client Ready');
    });

    this.redisClient.on('close', () => {
      this.logger.warn('Redis Client Connection Closed');
    });

    this.redisClient.on('reconnecting', () => {
      this.logger.log('Redis Client Reconnecting...');
    });

    await this.redisClient.connect();

    return this.redisClient;
  }

  async disconnect(): Promise<void> {
    if (this.redisClient) {
      await this.redisClient.disconnect();
      this.redisClient = null;
      this.logger.log('Redis Client Disconnected');
    }
  }

  async onModuleDestroy(): Promise<void> {
    await this.disconnect();
  }

  // Basic operations
  async get(key: string): Promise<string | null> {
    const client = await this.getClient();
    return client.get(key);
  }

  async set(key: string, value: string, ttl?: number): Promise<'OK'> {
    const client = await this.getClient();
    if (ttl) {
      // Convert milliseconds to seconds for Redis SETEX
      const ttlSeconds = Math.ceil(ttl / 1000);
      return client.setex(key, ttlSeconds, value);
    }
    return client.set(key, value);
  }

  async setex(key: string, ttl: number, value: string): Promise<'OK'> {
    const client = await this.getClient();
    // Convert milliseconds to seconds for Redis SETEX
    const ttlSeconds = Math.ceil(ttl / 1000);
    return client.setex(key, ttlSeconds, value);
  }

  async del(key: string): Promise<number> {
    const client = await this.getClient();
    return client.del(key);
  }

  async exists(key: string): Promise<number> {
    const client = await this.getClient();
    return client.exists(key);
  }

  // Sorted Set operations
  async zadd(key: string, score: number, member: string): Promise<number> {
    const client = await this.getClient();
    return client.zadd(key, score, member);
  }

  async zrange(key: string, start: number, stop: number): Promise<string[]> {
    const client = await this.getClient();
    return client.zrange(key, start, stop);
  }

  async zrangebyscore(key: string, min: number | string, max: number | string, limit?: { offset: number; count: number }): Promise<string[]> {
    const client = await this.getClient();
    if (limit) {
      return client.zrangebyscore(key, min, max, 'LIMIT', limit.offset, limit.count);
    }
    return client.zrangebyscore(key, min, max);
  }

  async zrem(key: string, ...members: string[]): Promise<number> {
    const client = await this.getClient();
    return client.zrem(key, ...members);
  }

  // Set operations
  async sadd(key: string, ...members: string[]): Promise<number> {
    const client = await this.getClient();
    return client.sadd(key, ...members);
  }

  async sismember(key: string, member: string): Promise<number> {
    const client = await this.getClient();
    return client.sismember(key, member);
  }

  // Hash operations
  async hset(key: string, field: string, value: string): Promise<number> {
    const client = await this.getClient();
    return client.hset(key, field, value);
  }

  async hget(key: string, field: string): Promise<string | null> {
    const client = await this.getClient();
    return client.hget(key, field);
  }

  async hgetall(key: string): Promise<Record<string, string>> {
    const client = await this.getClient();
    return client.hgetall(key);
  }

  async expire(key: string, ttl: number): Promise<number> {
    const client = await this.getClient();
    return client.expire(key, ttl);
  }

  /**
   * Get all keys matching a pattern using SCAN (safe for production)
   * @param pattern - Redis key pattern (e.g., 'device:lock:*')
   * @returns Array of matching keys
   */
  async keys(pattern: string): Promise<string[]> {
    const client = await this.getClient();
    const keys: string[] = [];
    let cursor = '0';

    do {
      const result = await client.scan(cursor, 'MATCH', pattern, 'COUNT', 100);
      cursor = result[0];
      keys.push(...result[1]);
    } while (cursor !== '0');

    return keys;
  }

  /**
   * Set key if not exists (atomic operation)
   * Returns 'OK' if key was set, null if key already exists
   */
  async setnx(key: string, value: string, ttl?: number): Promise<string | null> {
    const client = await this.getClient();
    if (ttl) {
      // Convert milliseconds to seconds for Redis SET with NX and EX
      const ttlSeconds = Math.ceil(ttl / 1000);
      const result = await client.set(key, value, 'EX', ttlSeconds, 'NX');
      return result;
    }
    const result = await client.set(key, value, 'NX');
    return result;
  }

  async pickFromZSet(key: string, numberOfJobs: number = 1) {
    const client = await this.getClient();

    const jobs = await client.zrange(key, 0, numberOfJobs - 1, 'WITHSCORES');

    if (!jobs || jobs.length === 0) {
      return [];
    }

    const result: Array<{
      jobId: string;
      score: string;
    }> = [];

    for (let i = 0; i < jobs.length; i += 2) {
      result.push({
        jobId: jobs[i],
        score: jobs[i + 1]
      })
    }

    return result;
  }
}

