import { Injectable, Logger } from '@nestjs/common';
import { AcbService } from './acb.service';
import { PgService } from './pg.service';
import { HdBankService } from './hdbank.service';
import { VietcombankService } from './vietcombank.service';
import { DeviceService } from '../device/device.service';
import { Withdrawal } from '../../common/interfaces/withdrawal.interface';
import { IBankService, IAnalyzeTransferBillResult } from './interfaces/bank-service.interface';
import { SessionManagementService } from './session-management.service';
import { DeviceLockService } from '../device/device-lock.service';
import { DeviceConfig } from '../device/dto/device-config.dto';
import { ErrorType, WithdrawalProcessingError } from '../withdrawal/withdrawal-errors';
import { TelegramService } from '../notifiction/telegram.service';
import { sleep } from '@/common/utils/time';
import { join } from 'path';
import { readdir, stat } from 'fs/promises';
import { existsSync } from 'fs';

/**
 * Bank Service Factory
 * Routes to appropriate bank-specific service based on bankCode
 */
@Injectable()
export class BankService {
  private readonly logger = new Logger('BankService');
  private readonly bankServices: Map<string, IBankService> = new Map();

  constructor(
    private readonly acbService: AcbService,
    private readonly pgService: PgService,
    private readonly hdBankService: HdBankService,
    private readonly vietcombankService: VietcombankService,
    private readonly deviceService: DeviceService,
    private readonly sessionManagement: SessionManagementService,
    private readonly deviceLock: DeviceLockService,
    private readonly telegramService: TelegramService,
  ) {
    // Register all bank services
    this.registerBankService(this.acbService);
    this.registerBankService(this.pgService);
    this.registerBankService(this.hdBankService);
    this.registerBankService(this.vietcombankService);
  }

  /**
   * Register a bank service
   */
  private registerBankService(service: IBankService): void {
    const bankCode = service.getBankCode().toUpperCase();
    this.bankServices.set(bankCode, service);
    this.logger.log(`Registered bank service: ${bankCode}`);
  }

  /**
   * Get bank service by bank code (public method for controller)
   */
  getBankService(bankCode: string): IBankService | null {
    const service = this.bankServices.get(bankCode.toUpperCase());

    if (!service) {
      throw new WithdrawalProcessingError(
        `Bank ${bankCode} not supported! Cannot find bank service out!`, 
        ErrorType.PERMANENT
      );
    }

    return service;
  }

  /**
   * Get all supported bank codes
   */
  getSupportedBanks(): string[] {
    return Array.from(this.bankServices.keys());
  }

  /**
   * Login to bank app on device (with locking)
   */
  async login(bankCode: string, deviceId: string, force: boolean = false): Promise<void> {

    if(force) {
      await this.deviceLock.releaseLock(deviceId);
    }

    // Check if device is already locked (any type)
    const isLocked = await this.deviceLock.isLocked(deviceId);
    if (isLocked) {
      const lock = await this.deviceLock.getLock(deviceId);
      throw new Error(
        `Device ${deviceId} is currently locked by ${lock?.lockType} (${lock?.lockedBy}). Cannot login.`
      );
    }

    // Acquire lock using unified format: operation:operationId
    const operationId = `login:${bankCode}:${deviceId}:${Date.now()}`;
    const lockAcquired = await this.deviceLock.acquireLock(
      deviceId,
      'login',
      operationId,
      4.5 * 60 * 1000 // 5 minutes for login process
    );

    if (!lockAcquired) {
      throw new Error(`Failed to acquire lock for device ${deviceId}`);
    }

    try {
      // Mark as logging in
      await this.sessionManagement.markLoggingIn(deviceId, bankCode);

      const service = this.getBankService(bankCode);
      if (!service) {
        throw new Error(`Bank service not found for bank code: ${bankCode}`);
      }

      this.logger.log(`[${deviceId}] Logging in to ${bankCode}`);
      await service.login(deviceId);

      // Record successful login
      await this.sessionManagement.recordLogin(deviceId, bankCode);
      
      this.logger.log(`[${deviceId}] Successfully logged in to ${bankCode}`);
    } catch (error) {
      // Clear session on error
      await this.sessionManagement.clearSession(deviceId, bankCode);
      throw error;
    } finally {
      // Release login lock
      await this.deviceLock.releaseLock(deviceId);
    }
  }

  /**
   * Login all active banks
   */
  async loginAllActiveBanks(): Promise<void> {
    try {
      const maxRetries = 6;
      const delayTime = 10000;
      let devices = [];

      for (let i = 0; i < maxRetries; i++) {
        this.logger.log(`[${i + 1}/${maxRetries}] Retrying to list devices on login...`);
        devices = await this.deviceService.listDevices();
        if (devices.length > 0) {
          break;
        }

        await sleep(delayTime);
      }

      const deviceConfigs = await this.deviceService.listDeviceConfigs();
      const activeDevices = devices.filter(device => device.status === 'active' && device.bankCode);
      const activeDeviceConfigs = deviceConfigs.filter(config => config.status === 'active' && config.bankCode);

      if (activeDevices.length < activeDeviceConfigs.length) {
        const disconnectedDevices = activeDeviceConfigs.filter(config => !activeDevices.some(device => device.udid === config.deviceId));

        const messageBlocks = disconnectedDevices.map(config => 
          `<b>Bank:</b> ${config.bankCode}\n<b>Account:</b> ${config.accountName}`
        ).join('\n\n');
        
        this.telegramService.sendMessage({
          text: ` <b>Devices disconnected: ${disconnectedDevices.length}</b>\n\n${messageBlocks}`,
          options: {
            parseMode: 'HTML',
          },
        });
      }

      if (activeDevices.length === 0) {
        this.logger.warn('No active devices found to login');
        return;
      }

      // Release transfer locks for all active devices before login
      for (const device of activeDevices) {
        const deviceId = device.udid;
        try {
          await this.deviceLock.releaseLockByType(deviceId, 'transfer');
        } catch (error) {
          this.logger.warn(`Failed to release transfer lock for device ${deviceId}: ${error.message}`);
        }
      }

      // Login all active devices
      const loginPromises: Promise<void>[] = [];
      for (const device of activeDevices) {
        const bankCode = device.bankCode.toUpperCase();
        const deviceId = device.udid;
        loginPromises.push(
          this.login(bankCode, deviceId, true).catch(error => {
            this.logger.error(`Failed to login ${bankCode} on device ${deviceId}: ${error.message}`);
          })
        );
      }

      await Promise.allSettled(loginPromises);
      this.logger.log(`Completed login process for all active banks`);
    } catch (error) {
      this.logger.error(`Error logging in all active banks: ${error.message}`);
      throw error;
    }
  }

  executeTransferWithQRCode(
    withdrawal: Withdrawal, 
    deviceConfig: DeviceConfig, 
    bankCode: string,
  ) {
    const service = this.getBankService(bankCode);

    return service.executeTransferWithQRCode(withdrawal, deviceConfig);
  }

  /**
   * Check if session is valid for a device
   */
  async isSessionValid(deviceId: string, bankCode: string): Promise<boolean> {
    return await this.sessionManagement.isSessionValid(deviceId, bankCode);
  }

  /**
   * Analyze transfer bill and return image with analysis result
   */
  analyzeTransferBill(bankCode: string, withdrawal: Withdrawal): Promise<IAnalyzeTransferBillResult | null> {
    const service = this.getBankService(bankCode);
    
    return service.analyzeTransferBill(bankCode, withdrawal);
  }

  /**
   * Check and refresh session if needed
   */
  async refreshSessionIfNeeded(deviceId: string, bankCode: string): Promise<boolean> {
    // Get session to check status
    const session = await this.sessionManagement.getSession(deviceId, bankCode);
    
    // Skip if device is already logging in
    if (session?.status === 'logging_in') {
      return false;
    }

    // Check if session needs refresh (within refresh window) OR if session is expired/missing
    const needsRefresh = await this.sessionManagement.needsRefresh(deviceId, bankCode);
    const isValidSession = await this.sessionManagement.isSessionValid(deviceId, bankCode);
    
    // Only proceed if session needs refresh OR session is invalid (expired/missing)
    if (!needsRefresh && isValidSession) {
      return false;
    }

    // Check if device is locked
    const isLocked = await this.deviceLock.isLocked(deviceId);
    if (isLocked) {
      this.logger.warn(`Cannot refresh session for device ${deviceId} - device is locked`);
      return false;
    }

    try {
      if (!isValidSession) {
        this.logger.log(`Session expired or missing for device ${deviceId}, bank ${bankCode}. Retrying login...`);
      } else {
        this.logger.log(`Refreshing session for device ${deviceId}, bank ${bankCode}`);
      }
      await this.login(bankCode, deviceId);
      return true;
    } catch (error) {
      this.logger.error(`Failed to refresh session for device ${deviceId}: ${error.message}`);
      return false;
    }
  }

  createQRCodeAndTransferTo(bankCode: string, deviceId: string, withdrawal: Withdrawal): Promise<void> {
    const service = this.getBankService(bankCode);

    return service.createQRCodeAndTransferTo(deviceId, withdrawal);
  }

  captureScreen(bankCode: string, deviceId: string, folderPath: string): Promise<void> {
    const service = this.getBankService(bankCode);
    return service.captureScreen(deviceId, { folderPath });
  }

  getBillImagePath(bankCode: string, folderPath: string): Promise<string | null> {
    const service = this.getBankService(bankCode);
    return service.getBillImagePath(folderPath);
  }

  createBillFolder(bankCode: string, folderPath: string): Promise<void> {
    const service = this.getBankService(bankCode);
    return service.createBillFolder(folderPath);
  }

  goToHomeScreenFromBill(deviceConfig: DeviceConfig): Promise<void> {
    const service = this.getBankService(deviceConfig.bankCode);

    return service.goToHomeScreenFromBill(deviceConfig);
  }

  /*
  * Only for testing purpose
  */
  async getLatestImagePath(
    folderPath: string,
    timeout: number = 10000,
    pollInterval: number = 500
  ): Promise<string | null> {
    if (!folderPath) {
      this.logger.warn('Folder path is null or empty');
      return null;
    }

    const startTime = Date.now();
    let attemptCount = 0;

    this.logger.debug(
      `Scanning folder for bill image: ${folderPath}, timeout: ${timeout}ms, pollInterval: ${pollInterval}ms`
    );

    while (Date.now() - startTime < timeout) {
      attemptCount++;

      try {
        // Kim tra th mc c tn ti khng
        if (!existsSync(folderPath)) {
          await sleep(pollInterval);
          continue;
        }

        const files = await readdir(folderPath);

        if (files.length === 0) {
          // TODO: Uncomment this when debugging
          // this.logger.debug(`No files found in folder: ${folderPath}, retrying... (attempt ${attemptCount})`);
          await sleep(pollInterval);
          continue;
        }

        // Ly thng tin tt c cc file
        const filesWithStat = await Promise.all(
          files.map(async file => {
            try {
              const fullPath = join(folderPath, file);
              const s = await stat(fullPath);
              return { fullPath, mtime: s.mtimeMs, size: s.size };
            } catch (statError) {
              // Skip files that can't be stat'd
              return null;
            }
          })
        );

        // Lc cc file hp l (size > 0)
        const validFiles = filesWithStat.filter(
          (f): f is { fullPath: string; mtime: number; size: number } => 
            f !== null && f.size > 0
        );

        if (validFiles.length === 0) {
          await sleep(pollInterval);
          continue;
        }

        // Sp xp theo thi gian modified mi nht
        validFiles.sort((a, b) => b.mtime - a.mtime);

        const latestFile = validFiles[0].fullPath;
        const elapsed = Date.now() - startTime;
        this.logger.log(
          `Found latest bill image: ${latestFile} after ${elapsed}ms (${attemptCount} attempts)`
        );

        return latestFile;
      } catch (error) {
        this.logger.debug(
          `Error scanning folder ${folderPath} (attempt ${attemptCount}): ${error.message}, retrying...`
        );
        await sleep(pollInterval);
      }
    }

    // Timeout - khng tm thy file
    const elapsed = Date.now() - startTime;
    this.logger.warn(
      `Timeout scanning folder for bill image: ${folderPath} after ${elapsed}ms (${attemptCount} attempts)`
    );
    return null;
  }
}


