import { Injectable, Logger } from '@nestjs/common';
import { RedisService } from '../../common/services/redis.service';
import { randomUUID } from 'crypto';

export type DeviceLockType = 'login' | 'transfer';

export interface DeviceLock {
  deviceId: string;
  lockType: DeviceLockType;
  lockedAt: number;
  lockedBy: string; // Unified format: 'operation:operationId' (e.g., 'login:ACB:deviceId:timestamp' or 'withdraw:withdrawalId:deviceId')
  ttl: number; // TTL in milliseconds
  lockId: string; // Unique lock ID for safe release
}

// Lua script for atomic unlock (only unlock if lockId matches)
const UNLOCK_SCRIPT = `
if redis.call("GET", KEYS[1]) == ARGV[1] then
  redis.call("DEL", KEYS[2])
  return redis.call("DEL", KEYS[1])
else
  return 0
end
`;

@Injectable()
export class DeviceLockService {
  private readonly logger = new Logger('DeviceLockService');
  private readonly REDIS_LOCK_PREFIX = 'device:lock:';
  private readonly DEFAULT_LOCK_TTL = 10 * 60 * 1000; // 10 minutes default

  constructor(private readonly redisService: RedisService) {}

  /**
   * Acquire lock for a device (atomic operation using SET NX EX)
   */
  async acquireLock(
    deviceId: string,
    lockType: DeviceLockType,
    lockedBy: string,
    ttl: number = this.DEFAULT_LOCK_TTL
  ): Promise<boolean> {
    try {
      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const metadataKey = `${this.REDIS_LOCK_PREFIX}${deviceId}:meta`;
      const lockId = randomUUID();
      const ttlSeconds = Math.ceil(ttl / 1000);

      // Use atomic SET NX EX to acquire lock
      // This ensures only one process can acquire the lock
      const client = await this.redisService.getClient();
      const result = await client.set(key, lockId, 'EX', ttlSeconds, 'NX');

      if (result === 'OK') {
        // Lock acquired successfully, store metadata
        const lockMetadata: Omit<DeviceLock, 'lockId'> = {
          deviceId,
          lockType,
          lockedAt: Date.now(),
          lockedBy,
          ttl,
        };
        await this.redisService.setex(metadataKey, ttlSeconds, JSON.stringify(lockMetadata));
        
        this.logger.log(`[${deviceId}] Acquired ${lockType} lock (${lockedBy}) with lockId ${lockId}`);
        return true;
      }

      // Lock already exists
      this.logger.warn(
        `[${deviceId}] Device is already locked, cannot acquire lock`
      );
      return false;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error acquiring lock: ${error.message}`);
      return false;
    }
  }

  /**
   * Release lock for a device (atomic operation using Lua script)
   */
  async releaseLock(deviceId: string): Promise<void> {
    try {
      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const metadataKey = `${this.REDIS_LOCK_PREFIX}${deviceId}:meta`;
      const client = await this.redisService.getClient();
      
      // Get current lockId before releasing
      const lockId = await client.get(key);
      
      if (!lockId) {
        // Lock doesn't exist, clean up metadata if exists
        await this.redisService.del(metadataKey);
        this.logger.debug(`[${deviceId}] Device is not locked, nothing to release`);
        return;
      }

      // Use Lua script to atomically check and delete only if lockId matches
      // This prevents releasing lock that was acquired by another process
      const result = await client.eval(
        UNLOCK_SCRIPT,
        2, // number of keys
        key, // KEYS[1] - lock key
        metadataKey, // KEYS[2] - metadata key
        lockId as string // ARGV[1] - lockId to verify
      ) as number;

      if (result === 1) {
        this.logger.log(`[${deviceId}] Released lock (lockId: ${lockId})`);
      } else {
        // Lock was already released or changed by another process
        this.logger.warn(`[${deviceId}] Lock was already released or changed (expected lockId: ${lockId})`);
      }
    } catch (error) {
      this.logger.error(`[${deviceId}] Error releasing lock: ${error.message}`, error.stack);
    }
  }

  /**
   * Get current lock for a device
   */
  async getLock(deviceId: string): Promise<DeviceLock | null> {
    try {
      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const lockId = await this.redisService.get(key);
      
      if (!lockId) {
        return null;
      }

      // Get metadata
      const metadataKey = `${this.REDIS_LOCK_PREFIX}${deviceId}:meta`;
      const metadataStr = await this.redisService.get(metadataKey);
      
      if (!metadataStr) {
        // Metadata missing but lock exists - return minimal info
        return {
          deviceId,
          lockType: 'transfer', // default
          lockedAt: Date.now(),
          lockedBy: 'unknown',
          ttl: this.DEFAULT_LOCK_TTL,
          lockId,
        };
      }

      const metadata = JSON.parse(metadataStr) as Omit<DeviceLock, 'lockId'>;
      return {
        ...metadata,
        lockId,
      };
    } catch (error) {
      this.logger.error(`[${deviceId}] Error getting lock: ${error.message}`);
      return null;
    }
  }

  /**
   * Check if device is locked
   */
  async isLocked(deviceId: string, excludeType?: DeviceLockType): Promise<boolean> {
    try {
      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const exists = await this.redisService.exists(key);
      
      if (!exists) {
        return false;
      }

      const lock = await this.getLock(deviceId);
      if (!lock) {
        return false;
      }

      // If excludeType is provided, allow if lock is of that type
      if (excludeType && lock.lockType === excludeType) {
        return false;
      }

      return true;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error checking lock status: ${error.message}`);
      // On error, assume not locked (fail open)
      return false;
    }
  }

  /**
   * Extend lock TTL (with ownership verification)
   */
  async extendLock(deviceId: string, additionalTtl: number): Promise<boolean> {
    try {
      const lock = await this.getLock(deviceId);
      
      if (!lock) {
        return false;
      }

      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const metadataKey = `${this.REDIS_LOCK_PREFIX}${deviceId}:meta`;
      const client = await this.redisService.getClient();
      const currentTtlSeconds = await client.ttl(key);
      
      if (currentTtlSeconds <= 0) {
        // Lock expired
        return false;
      }

      const newTtlSeconds = Math.ceil((currentTtlSeconds * 1000 + additionalTtl) / 1000);
      const expireResult = await client.expire(key, newTtlSeconds);
      
      if (expireResult === 1) {
        // Also extend metadata TTL
        await this.redisService.expire(metadataKey, newTtlSeconds);
        
        this.logger.log(`[${deviceId}] Extended lock by ${additionalTtl}ms`);
        return true;
      }
      
      return false;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error extending lock: ${error.message}`);
      return false;
    }
  }

  /**
   * Release lock by type (force release if lock type matches)
   */
  async releaseLockByType(deviceId: string, lockType: DeviceLockType): Promise<boolean> {
    try {
      const lock = await this.getLock(deviceId);
      
      if (!lock) {
        return false;
      }

      // Only release if lock type matches
      if (lock.lockType !== lockType) {
        return false;
      }

      // Force release by directly deleting the keys (no lockId verification needed)
      const key = `${this.REDIS_LOCK_PREFIX}${deviceId}`;
      const metadataKey = `${this.REDIS_LOCK_PREFIX}${deviceId}:meta`;
      
      await this.redisService.del(key);
      await this.redisService.del(metadataKey);
      
      this.logger.log(`[${deviceId}] Force released ${lockType} lock`);
      return true;
    } catch (error) {
      this.logger.error(`[${deviceId}] Error releasing ${lockType} lock: ${error.message}`);
      return false;
    }
  }

  /**
   * Get all device lock keys matching pattern device:lock:*
   * @returns Array of device lock keys (without the prefix)
   */
  async hasAnyLockKeys(): Promise<number> {
    try {
      const pattern = `${this.REDIS_LOCK_PREFIX}*`;
      const allKeys = await this.redisService.keys(pattern);
      
      // Filter out metadata keys (device:lock:*:meta) and extract deviceId
      const lockKeys = allKeys.filter(key => !key.endsWith(':meta'))
      
      return lockKeys.length;
    } catch (error) {
      this.logger.error(`Error getting all lock keys: ${error.message}`);
      return 0;
    }
  }
}

