import { config } from "./config";

/**
 * Converts an object of coordinates to percentage coordinates
 * Converts normalized coordinates (0-1) to percentage coordinates (0-100)
 * @param coordinatesObj - Object containing coordinate objects with x and y properties
 * @returns Object with all coordinate values multiplied by 100
 */
export const scaleCoordinates = <
	T extends Record<string, { x: number; y: number }>
>(coordinatesObj: T, factor: number = 100): T => {
	return Object.fromEntries(
		Object.entries(coordinatesObj).map(([key, value]) => [
			key,
			{
				x: Math.round(value.x * factor),
				y: Math.round(value.y * factor),
			},
		])
	) as T;
}

