import { Injectable, Logger, OnModuleInit, OnModuleDestroy } from '@nestjs/common';
import * as WebSocket from 'ws';
import { ICaptureScreenOptions, IHCSwipeOptions, IWebSocketService } from './interface/websocket.interface';
import { COMMON_COORDINATES, KEYBOARD_LAYOUT, NUMPAD_LAYOUT } from '@/common/constants/keyboard';
import { sleep } from '@/common/utils/time';
import { omit } from 'lodash';

export interface HcBoxRequest {
	action: string;
	comm?: any;
	sessionID?: string;
}

export interface HcBoxResponse {
	StatusCode: number;
	result: any;
}

@Injectable()
export class HCWebSocketService implements IWebSocketService, OnModuleInit, OnModuleDestroy {
	private readonly logger = new Logger('HCWebSocketService');
	private ws: WebSocket | null = null;
	private wsUrl: string;
	private reconnectAttempts = 0;
	private maxReconnectAttempts = 10;
	private reconnectDelay = 5000;
	// Sequential request queue - all requests are queued and processed one at a time
	private requestQueue: Array<{ 
		request: HcBoxRequest; 
		resolve: (value: any) => void; 
		reject: (error: any) => void;
		timeout: number;
	}> = [];
	private pendingRequests = new Map<string, { resolve: (value: any) => void; reject: (error: any) => void }>();
	private isConnected = false;
	private isProcessingRequest = false; // Flag to track if currently processing a request

	constructor() {
		this.wsUrl = 'ws://127.0.0.1:26608';
	}

	async onModuleInit() {
		await this.connect();
	}

	async onModuleDestroy() {
		await this.disconnect();
	}

	private async connect(): Promise<void> {
		return new Promise((resolve, reject) => {
			try {
				this.logger.log(`Connecting to HC BOX WebSocket: ${this.wsUrl}`);
				this.ws = new WebSocket(this.wsUrl);

				this.ws.on('open', () => {
					this.logger.log('HC BOX WebSocket connected');
					this.isConnected = true;
					this.reconnectAttempts = 0;
					// Start processing queue when connected
					this.processRequestQueue();
					resolve();
				});

				this.ws.on('message', (data: WebSocket.Data) => {
					try {
						const response: HcBoxResponse = JSON.parse(data.toString());
						this.handleMessage(response);
					} catch (error) {
						this.logger.error(`Error parsing WebSocket message: ${error.message}`);
					}
				});

				this.ws.on('error', (error) => {
					this.logger.error(`WebSocket error: ${error.message}`);
					this.isConnected = false;
					if (this.reconnectAttempts === 0) {
						reject(error);
					}
				});

				this.ws.on('close', () => {
					this.logger.warn('HC BOX WebSocket closed');
					this.isConnected = false;
					this.scheduleReconnect();
				});
			} catch (error) {
				this.logger.error(`Error creating WebSocket connection: ${error.message}`);
				reject(error);
			}
		});
	}

	private scheduleReconnect(): void {
		if (this.reconnectAttempts >= this.maxReconnectAttempts) {
			this.logger.error('Max reconnection attempts reached');
			return;
		}

		this.reconnectAttempts++;
		const delay = this.reconnectDelay * this.reconnectAttempts;
		this.logger.log(`Scheduling reconnect attempt ${this.reconnectAttempts} in ${delay}ms`);

		setTimeout(() => {
			this.connect().catch((error) => {
				this.logger.error(`Reconnection failed: ${error.message}`);
			});
		}, delay);
	}

	private async disconnect(): Promise<void> {
		if (this.ws) {
			this.ws.close();
			this.ws = null;
			this.isConnected = false;
		}
	}

	private handleMessage(response: HcBoxResponse): void {
		// Handle responses - use FIFO approach since API doesn't provide request IDs
		if (this.pendingRequests.size > 0) {
			const [firstKey, firstHandler] = this.pendingRequests.entries().next().value;
			this.pendingRequests.delete(firstKey);
			if (response.StatusCode === 200) {
				firstHandler.resolve(response.result);
			} else {
				firstHandler.reject(new Error(`HC BOX error: ${response.StatusCode}`));
			}
		} else {
			this.logger.warn(`Received response but no pending requests: ${JSON.stringify(response)}`);
		}
	}

	/**
	 * Process requests sequentially from queue
	 * Only one request is processed at a time to avoid timeout issues
	 */
	private async processRequestQueue(): Promise<void> {
		// If already processing a request or queue is empty, do nothing
		if (this.isProcessingRequest || this.requestQueue.length === 0) {
			return;
		}

		// If not connected, don't process (will process when connected)
		if (!this.isConnected || !this.ws) {
			return;
		}

		// Mark as processing
		this.isProcessingRequest = true;

		// Get first request from queue
		const { request, resolve, reject, timeout } = this.requestQueue.shift()!;

		try {
			// Send request and wait for response
			const result = await this._sendRequestOnce(request, timeout);
			resolve(result);
		} catch (error) {
			reject(error);
		} finally {
			// Mark as done processing
			this.isProcessingRequest = false;
			
			// Process next request if any
			if (this.requestQueue.length > 0) {
				// Use setImmediate to avoid stack overflow and allow event loop to process
				setImmediate(() => this.processRequestQueue());
			}
		}
	}

	/**
	 * Send a single WebSocket request (internal use only)
	 * This method sends the request and waits for response
	 */
	private async _sendRequestOnce(request: HcBoxRequest, timeout = 30000): Promise<any> {
		return new Promise((resolve, reject) => {
			// Generate unique request ID
			const requestId = Date.now().toString() + Math.random().toString(36).substr(2, 9);
			this.pendingRequests.set(requestId, { resolve, reject });

			const timeoutId = setTimeout(() => {
				this.pendingRequests.delete(requestId);
				reject(new Error('WebSocket request timeout'));
			}, timeout);

			const originalResolve = resolve;
			const originalReject = reject;

			resolve = (value: any) => {
				clearTimeout(timeoutId);
				originalResolve(value);
			};

			reject = (error: any) => {
				clearTimeout(timeoutId);
				originalReject(error);
			};

			this.pendingRequests.set(requestId, { resolve, reject });

			try {
				this.ws!.send(JSON.stringify(request));
			} catch (error) {
				this.pendingRequests.delete(requestId);
				clearTimeout(timeoutId);
				reject(error);
			}
		});
	}

	/**
	 * Send WebSocket request - all requests are queued and processed sequentially
	 * This ensures no concurrent requests that could cause timeout issues
	 */
	private async sendRequest(request: HcBoxRequest, timeout = 30000): Promise<any> {
		return new Promise((resolve, reject) => {
			// Add request to queue
			this.requestQueue.push({ request, resolve, reject, timeout });
			
			// Start processing queue (will do nothing if already processing)
			this.processRequestQueue();
		});
	}

	async listDevices(): Promise<any[]> {
		const response = await this.sendRequest({ action: 'list' });
		return Array.isArray(response) ? response : [];
	}

	async click(deviceIds: string | string[], x: number, y: number, duration = 0.2): Promise<string> {
		const deviceIdsStr = Array.isArray(deviceIds) ? deviceIds.join(',') : deviceIds;
		return this.sendRequest({
			action: 'click',
			comm: {
				deviceIds: deviceIdsStr,
				x,
				y,
				duration,
			},
		});
	}

	async swipe(
		deviceIds: string | string[],
		options: IHCSwipeOptions
	): Promise<string> {
		const deviceIdsStr = Array.isArray(deviceIds) ? deviceIds.join(',') : deviceIds;
		return this.sendRequest({
			action: 'swipe',
			comm: {
				deviceIds: deviceIdsStr,
				...options
			},
		});
	}

	async captureScreen(
		deviceId: string,
		options: ICaptureScreenOptions
	) {
		return this.sendRequest({
			action: 'screen',
			comm: {
				deviceIds: deviceId,
				...omit(options, 'folderPath'),
				filePath: options.folderPath,
			},
		});
	}

	async home(deviceIds: string | string[]): Promise<string> {
		const deviceIdsStr = Array.isArray(deviceIds) ? deviceIds.join(',') : deviceIds;
		return this.sendRequest({
			action: 'home',
			comm: {
				deviceIds: deviceIdsStr,
			},
		});
	}

	async launchApp(deviceIds: string | string[], bundleId: string): Promise<string> {
		const deviceIdsStr = Array.isArray(deviceIds) ? deviceIds.join(',') : deviceIds;
		return this.sendRequest({
			action: 'launchApp',
			comm: {
				deviceIds: deviceIdsStr,
				content: bundleId,
			},
		});
	}

	async killApp(deviceIds: string | string[], bundleId: string): Promise<string> {
		const deviceIdsStr = Array.isArray(deviceIds) ? deviceIds.join(',') : deviceIds;
		return this.sendRequest({
			action: 'killApp',
			comm: {
				deviceIds: deviceIdsStr,
				content: bundleId,
			},
		});
	}

	/**
	* Check if character is a number
	*/
	private isNumber(char: string): boolean {
		return /^[0-9]$/.test(char);
	}

	/**
	 * Check if character is a letter
	 */
	private isLetter(char: string): boolean {
		return /^[a-zA-Z]$/.test(char);
	}

	/**
	 * Check if character is a special character that only appears in number mode
	 * (e.g., @, !, etc.)
	 */
	private isSpecialCharInNumberMode(char: string): boolean {
		// List of special characters that only appear in number mode
		const specialChars = ['@', '!'];
		return specialChars.includes(char);
	}

	/**
	 * Switch keyboard mode (between letter and number)
	 */
	async switchKeyboardMode(deviceIds: string | string[]): Promise<void> {
		const { x, y } = COMMON_COORDINATES.KEYBOARD_SWITCH;
		this.logger.log(`Switching keyboard mode on device ${deviceIds} at coordinates (${x}, ${y})`);
		await this.click(deviceIds, x, y, 0.2);
		// Wait for keyboard to switch
		await sleep(500);
		this.logger.log(`Keyboard mode switched successfully on device ${deviceIds}`);
	}

	/**
	 * Press shift key for uppercase letter
	 * Note: After typing one uppercase letter, keyboard automatically returns to lowercase
	 */
	async pressShiftKey(deviceIds: string | string[]): Promise<void> {
		const { x, y } = COMMON_COORDINATES.SHIFT_KEY;
		this.logger.debug(`Pressing shift key on device ${deviceIds} at coordinates (${x}, ${y})`);
		await this.click(deviceIds, x, y, 0.2);
		// Small delay after pressing shift
		await sleep(200);
		this.logger.debug(`Shift key pressed successfully on device ${deviceIds}`);
	}

	/**
	 * Press return/enter key
	 * Note: This key can be used to:
	 * - Dismiss keyboard (exit input mode)
	 * - Submit/enter (depending on context)
	 */
	async pressReturnKey(deviceId: string): Promise<void> {
		const { x, y } = COMMON_COORDINATES.RETURN_KEY;
		this.logger.log(`Pressing return key on device ${deviceId} at coordinates (${x}, ${y})`);
		await this.click(deviceId, x, y, 0.2);
		// Wait for action to complete
		await sleep(500);
		this.logger.log(`Return key pressed successfully on device ${deviceId}`);
	}

	/**
	 * Input text value by clicking on keyboard keys
	 * Automatically switches between number and letter keyboard as needed
	 */
	async inputText(deviceIds: string | string[], text: string): Promise<void> {
		if (!text || text.length === 0) {
			throw new Error('Password cannot be empty');
		}

		// Track current keyboard mode
		// Default keyboard mode is 'letter' (bn phm mc nh l ch)
		let currentMode: 'number' | 'letter' = 'letter';

		// Input each character
		for (let i = 0; i < text.length; i++) {
			const char = text[i];
			const charLower = char.toLowerCase();
			const isUppercase = char !== charLower && this.isLetter(char);

			// For special characters (like @, !), use char directly (they don't have lowercase)
			// For other characters, use charLower to find coordinates
			const keyToLookup = this.isSpecialCharInNumberMode(char) ? char : charLower;
			const keyCoords = KEYBOARD_LAYOUT[keyToLookup];

			if (!keyCoords) {
				this.logger.warn(`Character '${char}' not found in keyboard layout. Skipping...`);
				continue;
			}

			// Determine required keyboard mode for this character
			// Special characters (like @, !) only appear in number mode
			let requiredMode: 'number' | 'letter' | null = null;
			if (this.isNumber(charLower)) {
				requiredMode = 'number';
			} else if (this.isLetter(charLower)) {
				requiredMode = 'letter';
			} else if (this.isSpecialCharInNumberMode(char)) {
				// Special characters only appear in number mode
				requiredMode = 'number';
			}

			// Switch keyboard mode if needed (letter <-> number)
			if (requiredMode && currentMode !== requiredMode) {
				this.logger.debug(`Switching keyboard to ${requiredMode} mode for character '${char}'`);
				await this.switchKeyboardMode(deviceIds);
				currentMode = requiredMode;
			}

			// If uppercase letter, press shift key first
			// Note: After typing one uppercase letter, keyboard automatically returns to lowercase
			if (isUppercase && currentMode === 'letter') {
				this.logger.debug(`Pressing shift key for uppercase letter '${char}'`);
				await this.pressShiftKey(deviceIds);
			}

			// Click the key
			// For special characters, use char directly; for others, use charLower coordinates
			this.logger.debug(`Clicking key '${keyToLookup}'${isUppercase ? ' (uppercase)' : ''} at (${keyCoords.x}, ${keyCoords.y})`);
			await this.click(deviceIds, keyCoords.x, keyCoords.y, 0.15);
			// Small delay between key presses
			await sleep(200);
		}

		this.logger.log(`Text input completed on device ${deviceIds}`);
	}

	/**
	 * Input number using numpad layout
	 * @param deviceId - Device ID
	 * @param value - Number string to input (e.g., "123", "456789")
	 */
	async inputNumpadNumber(deviceIds: string | string[], value: string) {
		if (!value || value.length === 0) {
			throw new Error('value cannot be empty');
		}

		for (let i = 0; i < value.length; i++) {
			const digit = value[i];

			const coordinates = NUMPAD_LAYOUT[digit];

			await this.click(deviceIds, coordinates.x, coordinates.y, 0.15);
			this.logger.debug(`Clicking key '${digit}' at (${coordinates.x}, ${coordinates.y})`);
			await sleep(200);
		}
	}

	async transferFile(deviceIds: string | string[], filePath: string, fileType: number = 1): Promise<any> {
	// TODO: Implement transfer file
	return Promise.resolve(null);
	}

	isConnectedStatus(): boolean {
		return this.isConnected;
	}
}


