import { Injectable, Logger } from '@nestjs/common';
import { Cron, CronExpression } from '@nestjs/schedule';
import { BankService } from './bank.service';
import { DeviceService } from '../device/device.service';
import { SessionManagementService } from './session-management.service';
import { RedisLockService } from '../redis-lock/redis-lock.service';
import { SYSTEM_LOCK_BOXPHONE } from '../withdrawal/constants/lock';

@Injectable()
export class SessionRefreshScheduler {
  private readonly logger = new Logger('SessionRefreshScheduler');

  constructor(
    private readonly bankService: BankService,
    private readonly deviceService: DeviceService,
    private readonly sessionManagement: SessionManagementService,
    private readonly redisLockService: RedisLockService,
  ) {}

  /**
   * Check and refresh sessions every 30 seconds
   */
  @Cron(CronExpression.EVERY_30_SECONDS)
  async refreshSessions() {
    try {

      const isNeedRestartBoxphone = await this.redisLockService.getLock(SYSTEM_LOCK_BOXPHONE);

      if (isNeedRestartBoxphone) {
        return;
      }

      const serviceStatus = await this.deviceService.getServiceStatus();
      if (serviceStatus !== 'active') {
        return; // Skip if service is not active
      }

      const devices = await this.deviceService.listDevices();
      const activeDevices = devices.filter(device => device.status === 'active' && device.bankCode);

      for (const device of activeDevices) {
        const bankCode = device.bankCode.toUpperCase();
        
        try {
          await this.bankService.refreshSessionIfNeeded(device.udid, bankCode);
        } catch (error) {
          this.logger.error(
            `Error refreshing session for device ${device.udid}, bank ${bankCode}: ${error.message}`
          );
        }
      }
    } catch (error) {
      this.logger.error(`Error in session refresh scheduler: ${error.message}`);
    }
  }
}

