import { Injectable, Logger } from '@nestjs/common';
import { HttpService } from '@nestjs/axios';
import { firstValueFrom } from 'rxjs';
import { AxiosError } from 'axios';
import * as FormData from 'form-data';
import { createReadStream } from 'fs';
import { config } from '../../common/constants/config';
import { sleep } from '../../common/utils/time';
import { BillAnalyzedStatus } from '@/modules/bank/interfaces/bank-service.interface';
import { IUpdateWithdrawal } from './interface/update-withdrawal';

@Injectable()
export class EzpayBeClientService {
  private readonly logger = new Logger('EzpayBeClientService');
  private readonly apiUrl: string;
  private readonly apiKey: string;

  constructor(
    private readonly httpService: HttpService
  ) {
    this.apiUrl = config.ezpayBe.apiUrl ?? 'http://localhost:3001';
    this.apiKey = config.ezpayBe.apiKey ?? '';
  }

  async completeWithdrawal(
    withdrawalId: string,
    payload: {
      analyzedStatus: BillAnalyzedStatus,
      sourceBankCode: string,
      sourceAccountNo: string,
      sourceAccountName: string,
    }
  ): Promise<boolean> {
    const maxRetries = 3;
    const baseDelay = 2000; // 2 seconds

    for (let attempt = 0; attempt <= maxRetries; attempt++) {
      try {
        const url = `${this.apiUrl}/api/internal/withdrawals/complete/${withdrawalId}`;

        this.logger.log(`Updating withdrawal ${withdrawalId} status to ${payload.analyzedStatus} (attempt ${attempt + 1}/${maxRetries + 1})`);

        const data = {
          analyzedStatus: payload.analyzedStatus,
          sourceBankCode: payload.sourceBankCode,
          sourceAccountNo: payload.sourceAccountNo,
          sourceAccountName: payload.sourceAccountName
        };

        const response = await firstValueFrom(
          this.httpService.patch(url, data, {
            headers: {
              'Content-Type': 'application/json',
              'X-API-Key': this.apiKey,
              'x-internal-secret-key': 'uHqE5X9H0QEBsqlZpdTHG0fOyvmMtHjCGgsG4ouls3Gz9WvaWg4w9HV3sqETGWfE',
            },
            timeout: 20000,
          }),
        );

        if (response.status === 200 || response.status === 204) {
          this.logger.log(`Successfully updated withdrawal ${withdrawalId} status to ${payload.analyzedStatus}`);

          return;
        }

        this.logger.warn(`Unexpected status code ${response.status} when updating withdrawal ${withdrawalId}`);
      } catch (error) {
        const axiosError = error as AxiosError;
        const payload = axiosError.response?.data;
        const errorMessage = axiosError.message;

        if (attempt < maxRetries) {
          const delay = baseDelay * Math.pow(2, attempt); // Exponential backoff
          this.logger.warn(
            `Failed to update withdrawal ${withdrawalId} (attempt ${attempt + 1}/${maxRetries + 1}): ${errorMessage}. Payload: ${JSON.stringify(payload)}, Retrying in ${delay}ms`,
          );
          await sleep(delay);
        } else {
          this.logger.error(
            `Failed to update withdrawal ${withdrawalId} after ${maxRetries + 1} attempts. Payload: ${JSON.stringify(payload)}, Error: ${errorMessage}`,
          );
        }
      }
    }
  }

  async uploadReceipt(withdrawalId: string, receiptPath: string) {
    const maxRetries = 3;
    const baseDelay = 2000; // 2 seconds

    for (let attempt = 0; attempt <= maxRetries; attempt++) {
      try {
        const url = `${this.apiUrl}/api/internal/withdrawals/upload-receipt/${withdrawalId}`;

        this.logger.log(`Uploading bill for withdrawal ${withdrawalId} (attempt ${attempt + 1}/${maxRetries + 1})`);

        if (!receiptPath) {
          this.logger.warn(`No receipt path provided for withdrawal ${withdrawalId}`);

          return;
        }

        const formdata = new FormData();
        formdata.append('receipt', createReadStream(receiptPath));

        const response = await firstValueFrom(
          this.httpService.patch(url, formdata, {
            headers: {
              ...formdata.getHeaders(),
              'X-API-Key': this.apiKey,
              'x-internal-secret-key': 'uHqE5X9H0QEBsqlZpdTHG0fOyvmMtHjCGgsG4ouls3Gz9WvaWg4w9HV3sqETGWfE',
            },
            timeout: 20000,
          }),
        );

        if (response.status === 200 || response.status === 204) {
          this.logger.log(`Successfully uploaded bill for withdrawal ${withdrawalId}`);

          return;
        }

        this.logger.warn(`Unexpected status code ${response.status} when uploading bill for withdrawal ${withdrawalId}`);
      } catch (error) {
        const axiosError = error as AxiosError;
        const response = axiosError.response?.data;
        const errorMessage = axiosError.message;

        if (attempt < maxRetries) {
          const delay = baseDelay * Math.pow(2, attempt); // Exponential backoff
          this.logger.warn(
            `Failed to upload bill for withdrawal ${withdrawalId} (attempt ${attempt + 1}/${maxRetries + 1}): ${errorMessage}. Response: ${JSON.stringify(response)}, Retrying in ${delay}ms`,
          );
          await sleep(delay);
        } else {
          this.logger.error(
            `Failed to upload bill for withdrawal ${withdrawalId} after ${maxRetries + 1} attempts. Response: ${JSON.stringify(response)}, Error: ${errorMessage}`,
          );
        }
      }
    }
  }

  updateWithdrawal(id: string, data: IUpdateWithdrawal) {
    this.logger.log(`Updating withdrawal ${id} data ${JSON.stringify(data)}`);

    const url = `${this.apiUrl}/api/internal/withdrawals/${id}`;

    return firstValueFrom(
      this.httpService.patch(url, data, {
        headers: {
          'x-internal-secret-key': 'uHqE5X9H0QEBsqlZpdTHG0fOyvmMtHjCGgsG4ouls3Gz9WvaWg4w9HV3sqETGWfE',
        },
      }),
    );
  }
}

